<template>
    <div class="map-region-next-inner">
        <div class="map-region-next-name">
            <span>下级区域</span>
            <span v-if="currentRegion.children.length>6" style="float:right;">
                <img v-if="index>0" class="map-region-next-img" :src="imgLeft" @click="handleBefore">
                <img v-if="index<maxIndex" class="map-region-next-img" :src="imgRight" @click="handleAfter">
            </span>
        </div>
        <div class="map-region-next-content">
            <el-row class="map-region-next-content-nodata" v-if="currentRegion.children.length==0">
                暂无数据
            </el-row>
            <el-row v-else type="flex" justify="space-around" v-for="(item,index) in itemData" :key="index">
                <el-col v-for="(col,cIndex) in item" :span="8" :key="cIndex">
                    <el-button @click="handleNext(col)" class="btn-color-green" size="mini" round>{{col.label}}</el-button>
                </el-col>
                <el-col v-if="item.length==2" v-for="(col,cIndex) in [1]" :span="8" :key="cIndex+4">
                </el-col>
                <el-col v-if="item.length==1" v-for="(col,cIndex) in [1,2]" :span="8" :key="cIndex+4">
                </el-col>
            </el-row>
        </div>
    </div>
</template>

<script>
    import {
        chunk,
        slice
    } from 'lodash'
    import {
        mapState,
        mapGetters
    } from 'vuex'

    export default {
        computed: {
            ...mapState('techo/map', ['activedNode']),
            ...mapGetters('techo/map', ['currentRegion']),
            itemData() {
                let len = this.currentRegion.children.length - this.index * 6 - 6 > 0 ? this.index * 6 + 6 : undefined;
                return this.chunk(this.slice(_.orderBy(this.currentRegion.children,'code'), this.index * 6, len), 3)
            }
        },
        data() {
            return {
                imgLeft: require("../images/cont_eject_return_left.png"),
                imgRight: require("../images/cont_eject_return_right.png"),
                chunk,
                slice,
                index: 0,
                maxIndex: 0
            }
        },
        created() {
            this.maxIndex = this.chunk(this.currentRegion.children, 6).length - 1;
        },
        methods: {
            handleAfter() {
                this.index = this.index + 1;
            },
            handleBefore() {
                this.index = this.index - 1;
            },
            // 跳转到下级区域
            handleNext(item) {
                let node = _.find(this.activedNode.childNodes, ['key', item.code]);
                this.$store.commit('techo/map/activedNodeSet', node);
            }
        },
        watch: {
            currentRegion(nValue, oValue) {
                this.maxIndex = this.chunk(this.currentRegion.children, 6).length - 1;
                this.index = 0;
            }
        }
    }
</script>

