<template>
    <div class="map-grid-wrapper">
        <div class="map-grid-header">
            <img src="">
            <span>{{currentRegion.label}}</span>
            <span class="map-grid-header-tool">
                <!-- <img class="map-region-header-tool-img" :src="imgLeft">
                <img class="map-region-header-tool-img" :src="imgRight"> -->
                <span @click="handleUp">
                    <techo-icon v-if="activedNode.level != 1" class="map-region-header-tool-icon" name="reply" alt="返回"></techo-icon>
                </span>
                <img class="map-grid-header-tool-img" @click="handleClose" :src="imgDelete">
            </span>
        </div>
        <!-- 网格信息 -->
        <div class="map-grid-wgy">
            <vRegionWgyCarousel></vRegionWgyCarousel>
        </div>
        <!-- 综治信息 -->
        <div class="map-grid-info">
            <div class="map-grid-info-name">
                <span class="map-grid-info-name-tab" :class="[tabIndex==0?'map-grid-info-name-tab-active':'']" @click="tabIndex=0">综治信息</span>
                <span class="map-grid-info-name-tab" :class="[tabIndex==1?'map-grid-info-name-tab-active':'']" @click="tabIndex=1">楼栋信息</span>
            </div>
            <vRegionDataInfo v-show="tabIndex==0"></vRegionDataInfo>
            <vRegionTableList v-show="tabIndex==1"></vRegionTableList>
        </div>
    </div>
</template>

<script>
    import {
        mapState,
        mapGetters
    } from 'vuex'
    import vRegionWgyCarousel from "./components/region-wgy-carousel.vue";
    import vRegionDataInfo from "./components/region-data-info.vue";
    import vRegionTableList from "./components/region-table-list.vue";

    export default {
        components: {
            vRegionDataInfo,
            vRegionWgyCarousel,
            vRegionTableList
        },
        data() {
            return {
                tabIndex: 0,
                imgLeft: require("./images/cont_eject_return_left.png"),
                imgRight: require("./images/cont_eject_return_right.png"),
                imgDelete: require("./images/cont_eject_delete.png")
            }
        },
        computed: {
            ...mapState('techo/map', ['activedNode']),
            ...mapGetters('techo/map', ['currentRegion']),
        },
        methods: {
            // 关闭面板
            handleClose() {
                this.$store.commit('techo/map/activedNodeSet', null);
            },
            // 返回上级节点页面
            handleUp() {
                this.$store.commit('techo/map/activedNodeSet', this.activedNode.parent);
            }
        }
    }
</script>

<style lang="scss">
    @import './styles/grid_data.scss';
</style>