<template>
    <div class="map-query-data-wrapper">
        <div class="map-query-data-header">
            <img src="">
            <span>数据专题</span>
            <span class="map-query-data-header-tool">
                <img class="map-query-data-header-tool-img" alt="关闭" @click="handleClose" :src="imgDelete">
            </span>
        </div>
        <div class="map-query-data-condition">
            <el-input placeholder="请输入名称" v-model="name" size="mini" class="input-with-select">
                <el-dropdown v-if="complexType" slot="prepend" szie="mini">
                    <span class="el-dropdown-link">
                        <span>{{abbrName}}</span>
                    </span>
                    <el-dropdown-menu slot="dropdown">
                        <el-dropdown-item @click.native="handleCommand(item)" v-for="(item,index) in find(complex,['name',complexType]).container"
                            :key="index">
                            <span class="el-dropdown-link">
                                {{item.value}}
                            </span>
                        </el-dropdown-item>
                    </el-dropdown-menu>
                </el-dropdown>
                <span v-else="find(complex,['name',queryDataType])" class="el-dropdown-link" slot="prepend">
                    <span>{{abbrName}}</span>
                </span>

                <el-button @click="handleQuery" slot="append" size="mini" icon="el-icon-search"></el-button>
            </el-input>
        </div>
        <div class="map-query-data-content">
            <div class="map-query-data-content-table">
                <el-table :page-size="pageSize" ref="table" size="medium" :data="tableData" tooltip-effect="dark"
                    header-cell-class-name="map-query-data-content-table-header" @row-click="handleRowClick">
                    <el-table-column label="序号" width="50" align="center">
                        <template slot-scope="scope">
                            <span><img style="height:24px;vertical-align:middle;" :src="['/image/map/'+(scope.$index+1)+'.png']"></span>
                            <!-- <el-tag :type="scope.row.tag === '家' ? 'primary' : 'success'" disable-transitions>{{scope.row.tag}}</el-tag> -->
                        </template>
                    </el-table-column>
                    <el-table-column v-for="(value, key, index) in header" :key="index" :label="key" :prop="value"
                        align="center" show-overflow-tooltip></el-table-column>
                    <!-- <el-table-column label="操作" fixed="right" align="center" width="130">
                    <template slot-scope="scope">
                        <el-button size="mini" class="techo-list__body-table-button-view" @click="handleView(scope.$index,scope.row)">查看</el-button>
                    </template>
                </el-table-column> -->
                </el-table>
            </div>
            <div class="map-query-data-content-pager">
                <el-pagination ref="pager" @size-change="handlePagerChange" @current-change="handlePagerChange" layout="total,pager"
                    :total="recordCount">
                </el-pagination>
            </div>
        </div>
        <div class="map-query-data-type">
            <ul>
                <li @click="handleSetType('房屋信息')" alt="房屋信息" :class="queryDataType=='房屋信息'?'map-query-data-type-item-avtive':''">
                    <span>房</span>
                </li>
                <li @click="handleSetType('户籍人口')" alt="户籍人口" :class="queryDataType=='户籍人口'?'map-query-data-type-item-avtive':''">
                    <span>户</span>
                </li>
                <li @click="handleSetType('流动人口')" alt="流动人口" :class="queryDataType=='流动人口'?'map-query-data-type-item-avtive':''">
                    <span>流</span>
                </li>
                <li @click="handleSetType('特殊人群')" alt="特殊人群" :class="queryDataType=='特殊人群'||complexType=='特殊人群'?'map-query-data-type-item-avtive':''">
                    <span>特</span>
                </li>
                <li @click="handleSetType('重点青少年')" alt="重点青少年" :class="queryDataType=='重点青少年'?'map-query-data-type-item-avtive':''">
                    <span>青</span>
                </li>
                <li @click="handleSetType('留守人员')" alt="留守人员" :class="queryDataType=='留守人员'?'map-query-data-type-item-avtive':''">
                    <span>留</span>
                </li>
                <li @click="handleSetType('境外人员')" alt="境外人员" :class="queryDataType=='境外人员'?'map-query-data-type-item-avtive':''">
                    <span>外</span>
                </li>
                <li @click="handleSetType('出租房')" alt="出租房" :class="queryDataType=='出租房'?'map-query-data-type-item-avtive':''">
                    <span>租</span>
                </li>
                <li @click="handleSetType('企业')" alt="企业" :class="queryDataType=='企业'?'map-query-data-type-item-avtive':''">
                    <span>企</span>
                </li>
                <li @click="handleSetType('学校')" alt="学校" :class="queryDataType=='学校'?'map-query-data-type-item-avtive':''">
                    <span>校</span>
                </li>
                <li @click="handleSetType('场所')" alt="场所" :class="queryDataType=='场所'?'map-query-data-type-item-avtive':''">
                    <span>场</span>
                </li>
            </ul>
        </div>
        <techo-details v-show="dlgVisible" ref="details" mapModule :title="dlgTitle" :configKey="configKey" :height="'60%'"></techo-details>
        <!-- 户信息组件 -->
        <vHouseView ref="vHouseView"></vHouseView>
    </div>
</template>

<script>
    import {
        find
    } from 'lodash'
    // 房屋信息
    const houseHeader = {
        房屋名称: "name",
    }
    // 户籍人口
    const hpHeader = {
        姓名: "name",
        性别: "dicMap.sex",
    }
    // 流动人口
    const flowHeader = {
        姓名: "name",
        性别: "dicMap.sex",
    }
    // 重点青少年
    const youngHeader = {
        姓名: "name",
        类型: "dicMap.personType",
    }
    // 留守人员
    const rearHeader = {
        姓名: "name",
        类型: "dicMap.rearPeopleType",
    }
    // 境外人员
    const overHeader = {
        外文名: "foreignName",
        中文姓名: "chinessName",
    }
    // 吸毒人员
    const drugHeader = {
        姓名: "name",
        管控情况: "dicMap.managementSituation",
    }
    // 社区矫正人员
    const commHeader = {
        姓名: "name",
        矫正类别: "dicMap.redressType",
    }
    // 刑满释放人员
    const releaseHeader = {
        姓名: "name",
        是否累犯: "dicMap.isRecidivism",
    }
    // 精神障碍患者
    const psyHeader = {
        姓名: "name",
        家庭经济状况: "dicMap.financialSituation",
    }
    // 艾滋病人员
    const aidsHeader = {
        姓名: "name",
        感染途径: "dicMap.infectionWay",
    }
    // 社会组织
    const socialHeader = {
        名称: "corportionName"
    }
    // 企业
    const companyHeader = {
        名称: "companyName",
    }
    // 学校
    const schoolHeader = {
        名称: "name",
        类型: "dicMap.type",
    }
    // 出租房
    const rentalHeader = {
        房屋地址: "address",
        出租用途: "dicMap.leaseUse",
    }

    // 列表标签
    const imgs = ['list_1', 'list_2', 'list_3', 'list_4', 'list_5', 'list_6',
        'list_7', 'list_8', 'list_9', 'list_10', 'list_11', 'list_12',
        'list_13', 'list_14', 'list_15'
    ]

    import {
        mapState,
        mapActions,
        mapGetters
    } from 'vuex'
    import BScroll from 'better-scroll'
    import service from '../../map_service'
    import themeService from '../../theme_service'
    import turfPointOnFeature from '@turf/point-on-feature';
    import turfBbox from '@turf/bbox';
    import {
        randomPoint
    } from '@turf/random';
    import booleanPointInPolygon from '@turf/boolean-point-in-polygon'

    import vHouseView from "@/views/building_house/house-info-view.vue";

    export default {
        components: {
            vHouseView
        },
        props: {
            mapApi: {
                type: Object
            }
        },
        data() {
            return {
                BS: null,
                find,
                imgDelete: require("./images/cont_eject_delete.png"),
                // 显示数据类型
                type: '房屋信息',
                // 列表数据
                tableData: [],
                // 列表字段
                header: [],
                // 总记录数
                recordCount: 0,
                // 列表每页记录数
                pageSize: 15,
                // 列表记录对应标签
                markers: [],
                // 查询输入框绑定值
                name: '',
                // 复杂类型配置项
                complex: [{
                    name: "特殊人群",
                    container: [{
                        value: "吸毒人员",
                        abbr: "毒"
                    }, {
                        value: "社区矫正人员",
                        abbr: "矫"
                    }, {
                        value: "刑满释放人员",
                        abbr: "刑"
                    }, {
                        value: "精神障碍患者",
                        abbr: "精"
                    }, {
                        value: "艾滋病人员",
                        abbr: "艾"
                    }]
                }],
                // 复杂类型
                complexType: '',
                dlgVisible: false,
                dlgTitle: "",
                configKey: "",
                buildInfo: {}
            }
        },
        mounted() {
            this.scrollInit();

            this.handleQuery(this.type)

        },
        computed: {
            // 当前的区域信息
            ...mapGetters('techo/map', ['currentRegion']),
            ...mapState('techo/map', [
                'queryDataType', 'themeClass', 'regionData', 'activedRight'
            ]),
            // 查询数据类型简写
            abbrName() {
                switch (this.type) {
                    // 取第一个字
                    case '房屋信息':
                    case '户籍人口':
                    case "流动人口":
                    case "特殊人群":
                    case "留守人员":
                    case "刑满释放人员":
                    case "精神障碍患者":
                    case "艾滋病人员":
                    case "企业":
                    case "场所":
                        return this.type.substr(0, 1)
                        // 取第二个字
                    case "境外人员":
                    case "出租房":
                    case "学校":
                    case "吸毒人员":
                        return this.type.substr(1, 1)
                    case "社区矫正人员":
                        return "矫"
                    case "重点青少年":
                        return "青"
                }
            }
        },
        methods: {
            // 关闭面板
            handleClose() {
                this.$store.commit('techo/map/activedRightSet', '');
                this.$store.commit('techo/map/themeClassSet', 'theme_resource')
            },
            // 滚动条初始化
            scrollInit() {
                this.BS = new BScroll(this.$el.querySelector(".map-query-data-content-table"), {
                    mouseWheel: true
                })
            },
            // 滚动条销毁
            scrollDestroy() {
                try {
                    this.BS.destroy()
                } catch (e) {
                    delete this.BS
                    this.BS = null
                }
            },
            // 设置查询数据类型
            handleSetType(type) {
                // let complexType = this.find(this.complex, ['name', type]);
                // if (complexType) {
                //     this.type = complexType.container[0].value
                //     this.complexType = type;
                // } else {
                //     this.type = type;
                //     this.complexType = ''
                // }

                this.type = type
            },
            // 分页查询
            handlePagerChange() {
                let page = this.$refs['pager'].internalCurrentPage ? this.$refs['pager'].internalCurrentPage : 1;
                this.handleQuery(this.queryDataType)
                // this.$emit('onPageChange', this.title, page)
            },
            handleCommand(item) {
                this.type = item.value
            },
            // 数据行点击事件
            handleRowClick(row) {
                let marker = _.find(this.markers, item => {
                    return item.props.id == row.id
                })

                // 定位到标记点
                if (marker && marker.lon && marker.lat) {
                    this.mapApi.mapOper.setCenterAndZoom([marker.lon, marker.lat], 19)
                }
            },
            // 获取面内随机点
            getRandomPoint(geometry) {
                let bbox = turfBbox(geometry)

                let point = randomPoint(1, {
                    bbox,
                })

                if (booleanPointInPolygon(point.features[0], geometry)) {
                    return point.features[0]
                } else {
                    return this.getRandomPoint(geometry)
                }
            },
            handleQuery() {
                let that = this

                if (this.activedRight == '') {
                    return
                }
                const page = this.$refs['pager'].internalCurrentPage ? this.$refs['pager'].internalCurrentPage : 1;
                // 查询条件
                const condition = {
                    code: this.currentRegion ? this.currentRegion.code : constants.REGION.CODE,
                    page,
                    pageSize: this.pageSize,
                    name: this.name,
                    successFunc: (data) => {
                        if (data.totalCount > 0) {
                            that.tableData = data.list;
                            that.recordCount = data.totalCount;
                        } else {
                            that.tableData = []
                            that.recordCount = 0;
                        }
                    }
                }

                switch (this.queryDataType) {
                    case '房屋信息':
                        this.header = houseHeader
                        service.getHouseListByCode(condition)
                        break
                    case '户籍人口':
                        this.header = hpHeader
                        service.getHpPeopleListByCode(condition)
                        break
                    case "流动人口":
                        this.header = flowHeader
                        service.getFlowPeopleListByCode(condition)
                        break;
                    case "吸毒人员":
                        this.header = drugHeader
                        service.getDrugPeopleListByCode(condition)
                        break;
                    case "社区矫正人员":
                        this.header = commHeader
                        service.getComcorPeopleListByCode(condition)
                        break;
                    case "刑满释放人员":
                        this.header = releaseHeader
                        service.getReleasePeopleListByCode(condition)
                        break;
                    case "精神障碍患者":
                        this.header = psyHeader
                        service.getWreckerPeopleListByCode(condition)
                        break;
                    case "艾滋病人员":
                        this.header = commHeader
                        service.getAidsPeopleListByCode(condition)
                        break;
                    case "重点青少年":
                        this.header = youngHeader
                        service.getAidsPeopleListByCode(condition)
                        break;
                    case "留守人员":
                        this.header = rearHeader
                        service.getRearPeopleListByCode(condition)
                        break;
                    case "境外人员":
                        this.header = overHeader
                        service.getOverPeopleListByCode(condition)
                        break;
                    case "出租房":
                        this.header = rentalHeader
                        service.getRentalHouseListByCode(condition)
                        break;
                    case "企业":
                        this.header = companyHeader
                        service.getGroupListByCode(condition)
                        break;
                    case "学校":
                        this.header = schoolHeader
                        service.getSchoolListByCode(condition)
                        break;
                    case "场所":
                        this.header = socialHeader
                        service.getSocialGroupListByCode(condition)
                        break;
                    default:
                        break
                }
            }
        },
        watch: {
            // 查询数据类型
            type(nValue, oValue) {
                this.$store.commit('techo/map/queryDataTypeSet', nValue)
            },
            // 查询数据类型
            queryDataType(nValue, oValue) {
                let complexType = this.find(this.complex, ['name', nValue]);
                if (complexType) {
                    this.type = complexType.container[0].value
                    this.complexType = nValue;
                } else {
                    let complex = this.find(this.complex, ['name', this.complexType])
                    if (this.type != nValue) {
                        this.type = nValue
                    }
                    if (complex && this.find(complex.container, ['value', nValue])) {

                    } else {
                        this.complexType = ""
                    }
                    this.handleQuery()
                }
            },
            // 切换行政区域，重新查询数据
            currentRegion(nValue, oValue) {
                this.handleQuery()
            },
            // 列表数据改变
            tableData(nValue, oValue) {
                let that = this
                let buildings = this.regionData["建筑物"];

                _.map(imgs, item => {
                    that.mapApi.markerApi.clearMarkers(item)
                })

                if (nValue && nValue.length > 0) {
                    that.markers = _.map(nValue, (item, index) => {
                        let x = item.x,
                            y = item.y;

                        if (!x || !y) {
                            let building = _.find(buildings, build => {
                                return build.properties.CODE == item.buildingCode
                            })

                            if (building) {
                                let point = that.getRandomPoint(building.geometry)
                                x = point.geometry.coordinates[0]
                                y = point.geometry.coordinates[1]
                            }
                        }

                        item.x = x;
                        item.y = y;

                        return {
                            type: 'list_' + (index + 1),
                            lon: x,
                            lat: y,
                            dataType: this.queryDataType,
                            props: item
                        }
                    });
                    that.mapApi.markerApi.addMarkers(
                        that.markers, [],
                        (event) => {
                            let props = event.features[0].properties
                            let type = that.queryDataType

                            switch (type) {
                                // TODO 弹出房屋信息详情页面
                                case "房屋信息":
                                    that.$refs["vHouseView"].dialogVisible = true;
                                    that.$refs["vHouseView"].houseInfo = props;
                                    return
                                case "户籍人口":
                                    that.configKey = "housePeople"
                                    that.dlgTitle = "户籍人口"
                                    break
                                case "流动人口":
                                    that.configKey = "flowPeople"
                                    that.dlgTitle = "流动人口"
                                    break
                                case "重点青少年":
                                    that.configKey = "imptantYoungsters"
                                    that.dlgTitle = "重点青少年"
                                    break
                                case "留守人员":
                                    that.configKey = "rearPeople"
                                    that.dlgTitle = "留守人员"
                                    break
                                case "境外人员":
                                    that.configKey = "overseasPeople"
                                    that.dlgTitle = "境外人员"
                                    break
                                case "吸毒人员":
                                    that.configKey = "drugsterPeople"
                                    that.dlgTitle = "吸毒人员"
                                    break
                                case "社区矫正人员":
                                    that.configKey = "communityPeople"
                                    that.dlgTitle = "社区矫正人员"
                                    break
                                case "刑满释放人员":
                                    that.configKey = "releasedPeople"
                                    that.dlgTitle = "刑满释放人员"
                                    break
                                case "精神障碍患者":
                                    that.configKey = "psychoticPeople"
                                    that.dlgTitle = "精神障碍患者"
                                    break
                                case "艾滋病人员":
                                    that.configKey = "aidsPeople"
                                    that.dlgTitle = "艾滋病人员"
                                    break
                                case "出租房":
                                    that.configKey = "rentalHouse"
                                    that.dlgTitle = "出租房屋"
                                    break
                                case "企业":
                                    that.configKey = "unPublicEco"
                                    that.dlgTitle = "新经济组织"
                                    break
                                case "场所":
                                    that.configKey = "groupSocial"
                                    that.dlgTitle = "新社会组织"
                                    break
                                case "学校":
                                    that.configKey = "schoolInfo"
                                    that.dlgTitle = "校园安全"
                                    break
                                default:
                                    break
                            }

                            that.dlgVisible = true;
                            that.$nextTick(function () {
                                that.$refs["details"].data = props;
                                that.$refs["details"].visible = true;
                            });
                            // event.stopPropagation();

                        }
                    )
                }
            },
            // 隐藏数据专题面板时
            themeClass(nValue, oValue) {
                let that = this
                if (nValue != 'theme_data') {
                    _.map(imgs, item => {
                        that.mapApi.markerApi.clearMarkers(item)
                    })
                } else if (nValue == 'theme_data') {
                    // 初始化面板时查询加载数据
                    this.handleQuery()
                }
            }
        }
    }
</script>

<style lang="scss">
    @import './styles/query-data.scss';
</style>