<template>
    <div class="map-region-wrapper">
        <div class="map-region-header">
            <img src="">
            <span>{{currentRegion.label}}</span>
            <span class="map-region-header-tool">
                <span @click="handleUp">
                    <techo-icon v-if="activedNode.level != 1" class="map-region-header-tool-icon" name="reply" alt="返回上级"></techo-icon>
                </span>
                <img class="map-region-header-tool-img" @click="handleClose" :src="imgDelete" alt="关闭">
            </span>
        </div>
        <!-- 网格信息 -->
        <div class="map-region-wg">
            <vRegionDataCount></vRegionDataCount>
        </div>
        <!-- 下级区域 -->
        <div class="map-region-next">
            <vRegionDataNext></vRegionDataNext>
        </div>
        <!-- 机构信息 -->
        <div class="map-region-org">
            <div class="map-region-org-name">
                综治中心
            </div>
            <div class="map-region-org-content">
                <el-row type="flex" justify="space-around">
                    <el-col :span="8">
                        <img :src="orgInfo.photo?orgInfo.photo:imgDefaultOrg" height="120" alt="">
                    </el-col>
                    <el-col :span="16" class="map-region-org-content-border">
                        <el-row type="flex" justify="space-around">
                            <el-col :span="8" class="column-title">
                                名称
                            </el-col>
                            <el-col :span="16">
                                {{orgInfo.name}}
                            </el-col>
                        </el-row>
                        <el-row type="flex" justify="space-around">
                            <el-col :span="8" class="column-title">
                                联系电话
                            </el-col>
                            <el-col :span="16">
                                {{orgInfo.contact}}
                            </el-col>
                        </el-row>
                        <el-row type="flex" justify="space-around">
                            <el-col :span="8" class="column-title">
                                所在地址
                            </el-col>
                            <el-col :span="16">
                                {{orgInfo.localityDetail}}
                            </el-col>
                        </el-row>
                        <el-row type="flex" justify="space-around">
                            <el-col :span="8" class="column-title">
                                负责人
                            </el-col>
                            <el-col :span="16">
                                {{orgInfo.leaderName}}&nbsp;{{orgInfo.leaderContact}}
                            </el-col>
                        </el-row>
                    </el-col>
                </el-row>
            </div>
        </div>
        <!-- 综治信息 -->
        <div class="map-region-info">
            <div class="map-region-info-name">
                综治信息
            </div>
            <vRegionDataInfo></vRegionDataInfo>
        </div>
    </div>
</template>

<script>
    import {
        mapState,
        mapGetters
    } from 'vuex'
    import service from "../../map_service"

    import vRegionDataCount from "./components/region-data-count.vue";
    import vRegionDataNext from "./components/region-data-next.vue";
    import vRegionDataInfo from "./components/region-data-info.vue";
    export default {
        components: {
            vRegionDataInfo,
            vRegionDataCount,
            vRegionDataNext
        },
        data() {
            return {
                imgLeft: require("./images/cont_eject_return_left.png"),
                imgRight: require("./images/cont_eject_return_right.png"),
                imgDelete: require("./images/cont_eject_delete.png"),
                imgDefaultOrg: require("./images/下载.png"),
                // 综治中心
                orgInfo: {}
            }
        },
        created() {
            this.handleQueryZzCenter()
        },
        computed: {
            ...mapState('techo/map', ['activedNode']),
            ...mapGetters('techo/map', ['currentRegion']),
        },
        methods: {
            // 关闭面板
            handleClose() {
                this.$store.commit('techo/map/activedNodeSet', null);
            },
            // 返回上级节点页面
            handleUp() {
                this.$store.commit('techo/map/activedNodeSet', this.activedNode.parent);
            },
            // 查询综治中心信息
            handleQueryZzCenter() {
                let that = this;


                service.getZzCenterByCode({
                    code: this.currentRegion.code,
                    successFunc: (data) => {
                        that.orgInfo = data;
                    }
                })
            }
        },
        watch: {
            activedNode(nValue, oValue) {
                if (nValue) {
                    this.orgInfo = {}
                    this.handleQueryZzCenter()
                }
            }
        }
    }
</script>

<style lang="scss">
    @import './styles/region_data.scss';
</style>