<template>
    <techo-container class="map-view-wrapper" type="full" better-scroll>
        <!-- 地图组件 -->
        <vBaseMap ref="map" @onLoad="handleMapLoaded"></vBaseMap>
        <!-- 市、县、街道、社区四级区域信息面板 -->
        <transition name="el-zoom-in-center">
            <vRegionData v-if="activedLeft=='mapRegion'"></vRegionData>
        </transition>
        <!-- 网格信息面板 -->
        <transition name="el-zoom-in-center">
            <vGridData v-if="activedLeft=='mapGridRegion'" ref="gridRegion"></vGridData>
        </transition>
        <!-- 楼栋信息面板 -->
        <el-collapse-transition>
            <vHouseData v-if="activedLeft=='mapHouse'" ref="house"></vHouseData>
        </el-collapse-transition>
        <!-- 房屋户人员信息面板 -->
        <transition name="el-zoom-in-top">
            <vHouseUser v-show="activedHouseHolder" ref="houseUser"></vHouseUser>
        </transition>
        <!-- 专题图层面板 -->
        <transition name="el-zoom-in-center">
            <vThemeData @onLayerAdd="handleThemeAdd" @onLayerRemove="handleThemeRemove" v-show="activedRight=='mapTheme'"
                ref="theme" :mapApi="mapApi"></vThemeData>
        </transition>
        <transition name="el-zoom-in-center">
            <vQueryData v-show="activedRight=='mapData'" ref="query" :mapApi="mapApi"></vQueryData>
        </transition>
        <transition name="el-zoom-in-center">
            <vThemeSwitch v-show="activedRight=='mapTheme' || activedRight=='mapData'"></vThemeSwitch>
        </transition>
        <div class="map-toolbar-wrapper">
            <!-- 区域选择树信息面板 -->
            <div class="map-tool-tree-wrapper">
                <el-popover popper-class="map-tree-popover" v-model="mapTree" visible-arrow="false" placement="bottom"
                    trigger="click" @after-enter="handleShowTree" @after-leave="handleHideTree">
                    <span slot="reference" class="map-toolbar-btn">
                        <img :src="imgLocation" alt="铜川市">
                        <span>{{regionName}}</span>
                    </span>
                    <el-tree ref="regionTree" :data="defaultRegion" node-key="code" :expand-on-click-node="false"
                        :default-expanded-keys="expandedKeys" @node-click="handleTreeNodeClick"></el-tree>
                </el-popover>
            </div>
            <!-- 面包屑组件，显示当前选中区域位置 -->
            <vBreadCrumb @onNodeClick="handleTreeNodeClick"></vBreadCrumb>
            <!-- 工具—》打开专题图层 -->
            <div class="map-tool-theme">
                <span class="map-toolbar-btn" @click="handleThemeClick">
                    <img :src="imgTheme" alt="专题图层">
                    <span>专题图层</span>
                </span>
            </div>
            <!-- 工具—》地图基础工具 -->
            <div class="map-tool-toolbar">
                <el-popover popper-class="map-toolbar-popover" v-model="mapTool" visible-arrow="false" placement="bottom"
                    trigger="click">
                    <span slot="reference" class="map-tools-btn">
                        <img :src="imgTool" alt="工具"> 工具
                    </span>
                    <ul class="map-toobar-uk-nav">
                        <li class="popover-item" @click="handlePan" @mouseover="handleToolOver('isPanHover')" @mouseout="handleToolOut('isPanHover')">
                            <img :src="isPanHover?imgPanHover:imgPan" alt="">
                            <span>平移</span>
                        </li>
                        <li class="popover-item" @click="handleClear" @mouseover="handleToolOver('isClearHover')"
                            @mouseout="handleToolOut('isClearHover')">
                            <img :src="isClearHover?imgClearHover:imgClear" alt="">
                            <span>清除</span>
                        </li>
                        <li class="popover-item" @click="handleZoomIn" @mouseover="handleToolOver('isZoomInHover')"
                            @mouseout="handleToolOut('isZoomInHover')">
                            <img :src="isZoomInHover?imgZoomInHover:imgZoomIn" alt="">
                            <span>放大</span>
                        </li>
                        <li class="popover-item" @click="handleZoomOut" @mouseover="handleToolOver('isZoomOutHover')"
                            @mouseout="handleToolOut('isZoomOutHover')">
                            <img :src="isZoomOutHover?imgZoomOutHover:imgZoomOut" alt="">
                            <span>缩小</span>
                        </li>
                        <li class="popover-item" @click="handleFullExtent" @mouseover="handleToolOver('isFullHover')"
                            @mouseout="handleToolOut('isFullHover')">
                            <img :src="isFullHover?imgFullHover:imgFull" alt="">
                            <span>全图</span>
                        </li>
                        <li class="popover-item" @click="handleMeasureLength" @mouseover="handleToolOver('isLengthHover')"
                            @mouseout="handleToolOut('isLengthHover')">
                            <img :src="isLengthHover?imgLengthHover:imgLength" alt="">
                            <span>测距</span>
                        </li>
                        <li class="popover-item" @click="handleMeasureArea" @mouseover="handleToolOver('isAreaHover')"
                            @mouseout="handleToolOut('isAreaHover')">
                            <img :src="isAreaHover?imgAreaHover:imgArea" alt="">
                            <span>测面积</span>
                        </li>
                    </ul>
                </el-popover>
            </div>
            <!-- 工具—》打开分级区域 -->
            <div class="map-tool-region">
                <el-popover popper-class="map-grid-popover" v-model="mapGrid" visible-arrow="false" placement="bottom"
                    trigger="click">
                    <span slot="reference" class="map-toolbar-btn">
                        <img :src="imgGrid" alt="网格">
                        <span>网格</span>
                    </span>
                    <ul class="map-toobar-uk-nav">
                        <el-checkbox v-model="isVisibleBuilding">楼栋</el-checkbox>
                        <!-- <div class="map-toobar-uk-nav-line"></div> -->
                        <el-radio-group v-model="rdValue" @change="handleLayerChange">
                            <el-radio v-for="(item,index) in currentLayers" class="popover-item" :label="item" :key="index">{{item.split('-')[1]}}</el-radio>
                        </el-radio-group>
                    </ul>
                </el-popover>
            </div>
            <!-- 工具—》打开分级区域 -->
            <div class="map-tool-region">
                <el-popover popper-class="map-query-popover" v-model="mapQuery" visible-arrow="false" placement="bottom"
                    trigger="click">
                    <span slot="reference" class="map-toolbar-btn">
                        <img :src="imgQuery" alt="快速检索">
                        <span>快速检索</span>
                    </span>
                    <ul class="map-toobar-uk-nav">
                        <el-autocomplete popper-class="map-house-autocomplete" v-model="houseName" :fetch-suggestions="querySearchAsync"
                            placeholder="请输入内容" @select="handleSelectHouse">
                            <!-- <i class="el-icon-query el-input__icon" slot="suffix" @click="handleSelectHouse">
                            </i> -->
                            <template slot-scope="{ item }">
                                <div class="map-house-autocomplete-name">{{ item.name }}</div>
                                <span class="map-house-autocomplete-addr">{{ item.address }}</span>
                            </template>
                        </el-autocomplete>
                    </ul>
                </el-popover>
            </div>
            <!-- 工具—》矢量与影像底图切换 -->
            <div class="map-tool-toolbar">
                <el-popover popper-class="map-toolbar-popover" v-model="mapSwitch" visible-arrow="false" placement="bottom"
                    trigger="click">
                    <span v-if="baseMapType=='vector'" slot="reference" class="map-tools-btn">
                        <img :src="imgVector" alt="矢量底图"> 矢量底图
                    </span>
                    <span v-else slot="reference" class="map-tools-btn">
                        <img :src="imgImage" alt="影像底图"> 影像底图
                    </span>
                    <ul class="map-toobar-uk-nav">
                        <li class="popover-item" @click="handleSwitch('vector')" @mouseover="handleToolOver('isVectorHover')"
                            @mouseout="handleToolOut('isVectorHover')">
                            <span>矢量底图</span>
                        </li>
                        <li class="popover-item" @click="handleSwitch('image')" @mouseover="handleToolOver('isImageHover')"
                            @mouseout="handleToolOut('isImageHover')">
                            <span>影像底图</span>
                        </li>
                    </ul>
                </el-popover>
            </div>
        </div>
        <techo-details v-if="dlgVisible" ref="details" mapModule :title="dlgTitle" :configKey="configKey" :height="'60%'"></techo-details>
    </techo-container>
</template>

<script>
    import {
        mapState,
        mapGetters
    } from 'vuex'
    import BScroll from 'better-scroll'

    import elementResizeEvent from "element-resize-event"
    // 引入子组件
    import vBaseMap from '@/map/base-map.vue'
    import vRegionData from './components/region_data/region-data.vue'
    import vGridData from './components/region_data/grid-data.vue'
    import vHouseData from './components/region_data/house-data.vue'
    import vThemeData from './components/theme_data/theme-data.vue'
    import vThemeSwitch from './components/theme_data/theme-switch.vue'
    import vQueryData from './components/query_data/query-data.vue'
    import vHouseUser from './components/region_data/components/house-user.vue'
    import vBreadCrumb from './components/breadcrumb/breadcrumb.vue'

    import service from './map_service'
    import themeService from './theme_service'

    export default {
        components: {
            vBaseMap,
            vRegionData,
            vGridData,
            vHouseData,
            vThemeData,
            vThemeSwitch,
            vQueryData,
            vHouseUser,
            vBreadCrumb
        },
        data() {
            return {
                // 地图模块入口
                mapApi: null,
                // 是否显示区域下拉框
                mapTree: false,
                // 默认展开一级节点
                expandedKeys: ['610200'],
                // 是否显示工具下拉框
                mapTool: false,
                /** 地图工具栏图标 */
                imgTool: require("./images/工具.png"),
                // 平移图标
                isPanHover: false,
                imgPan: require("./images/map_tool_pan_default.png"),
                imgPanHover: require("./images/map_tool_pan_choose.png"),
                // 放大图标
                isZoomInHover: false,
                imgZoomIn: require("./images/map_tool_zoomin_default.png"),
                imgZoomInHover: require("./images/map_tool_zoomin_choose.png"),
                // 缩小图标
                isZoomOutHover: false,
                imgZoomOut: require("./images/map_tool_zoomout_default.png"),
                imgZoomOutHover: require("./images/map_tool_zoomout_choose.png"),
                // 全图图标
                isFullHover: false,
                imgFull: require("./images/map_tool_full_default.png"),
                imgFullHover: require("./images/map_tool_full_choose.png"),
                // 测距图标
                isLengthHover: false,
                imgLength: require("./images/map_tool_ranging_default.png"),
                imgLengthHover: require("./images/map_tool_ranging_choose.png"),
                // 测面积图标
                isAreaHover: false,
                imgArea: require("./images/map_tool_area_default.png"),
                imgAreaHover: require("./images/map_tool_area_choose.png"),
                // 清除图标
                isClearHover: false,
                imgClear: require("./images/map_tool_area_default.png"),
                imgClearHover: require("./images/map_tool_area_choose.png"),
                // 定位图标
                imgLocation: require("./images/map_top_coordinate.png"),
                // 网格图标
                mapGrid: false,
                imgGrid: require("./images/网格.png"),
                // 专题图层
                imgTheme: require("./images/专题图层.png"),
                mapComponent: false,
                // 快速检索
                mapQuery: false,
                imgQuery: require("./images/搜索.png"),
                // 矢量及影像切换
                baseMapType: 'vector',
                mapSwitch: false,
                imgVector: require("./images/矢量.png"),
                imgImage: require("./images/影像.png"),
                imgComponent: require("./images/content_map_case_near.png"),
                imgOperZoomIn: require("./images/content_map_zoomin.png"),
                imgOperZoomOut: require("./images/content_map_zoomout.png"),
                imgOperFull: require("./images/content_map_full.png"),
                // 顶级行政区划名称
                regionName: constants.REGION.NAME,
                // 网格
                rdValue: '',
                // 是否显示楼栋
                isVisibleBuilding: true,
                // 检索输入框房屋名称
                houseName: '',
                autoHouseList: [],
                // 是否绘图工具可用
                isShowDraw: false,
                // 区域树面板滚动轴
                BS: null,
                // 详情弹出框是否可见
                dlgVisible: false,
                // 详情弹出框标题
                dlgTitle: "",
                // 详情弹出框配置项
                configKey: '',
                // 判断当前地图是否处于绘图工具操作状态
                isMapDrawing: false,
            };
        },
        computed: {
            // 激活的左侧面板、激活的右侧面板、激活的树节点 
            ...mapState('techo/map', [
                'activedLeft', 'activedRight', 'activedNode', 'defaultRegion', 'activedHouse', 'houseList',
                'defaultRegion', 'currentLayer', 'centerData', 'activedHouseHolder', 'activedLayers', 'themeType',
                'themeClass'
            ]),
            // 当前的区域信息
            ...mapGetters('techo/map', ['currentRegion', 'currentLayers']),
        },
        created() {
            this.$store.commit('techo/map/defaultRegionLoad')
        },
        mounted() {
            let that = this
            this.mapApi = this.$refs["map"].mapApi

            window.mapApi = this.mapApi

            // 地图比例缩放回调函数
            this.mapApi.mapOper.addMapEvent('zoomend', function (zoom) {
                that.handleMapZoom(zoom)
            })

            // 选中默认
            if (this.$route.params.name != undefined) {
                // 此处延迟1秒钟用来保证地图图层已经完全加载
                setTimeout(() => {
                    this.$store.commit('techo/map/activedRightSet', 'mapTheme');
                    this.$nextTick(function () {
                        that.$refs.theme.handleCheckNode([that.$route.params.name]);
                    })
                }, 1000);
            }
        },
        methods: {
            // 平移
            handlePan() {
                this.mapApi.setMapPan();
                this.mapTool = false;
            },
            // 清除
            handleClear() {
                this.mapApi.clear();
                this.mapTool = false;
            },
            // 全图
            handleFullExtent() {
                this.mapApi.mapOper.setFullExtent();
                this.mapTool = false;
            },
            // 放大一级
            handleZoomIn() {
                this.mapApi.mapOper.zoomIn();
                this.mapTool = false;
            },
            // 缩小一级
            handleZoomOut() {
                this.mapApi.mapOper.zoomOut();
                this.mapTool = false;
            },
            // 测距
            handleMeasureLength() {
                this.mapApi.measureApi.activeMeasureLength();
                this.mapTool = false;
            },
            // 测面积
            handleMeasureArea() {
                this.mapApi.measureApi.activeMeasureArea();
                this.mapTool = false;
            },
            // 矢量与影像底图切换
            handleSwitch(type) {
                this.baseMapType = type
                this.mapSwitch = false
            },
            // 设置部件图层可见性
            handleLayerVisibility(node, isChecked) {
                if (!node.children) {
                    this.$refs["map"].map.setLayerVisibility(node.layer, isChecked);
                }
            },
            // 鼠标移入工具高亮
            handleToolOver(name) {
                this[name] = true;
            },
            // 鼠标移出工具取消高亮
            handleToolOut(name) {
                this[name] = false;
            },
            // 激活绘图工具
            handelDraw(type) {
                this.isMapDrawing = true;
                this.$refs["map"].mapApi.deactiveDraw();
                this.$refs["map"].mapApi.activeDraw({
                    type,
                    endFunc: geo => {
                        debugger;
                    }
                });
            },
            // 区域树面板显示事件
            handleShowTree() {
                this.scrollInit()
            },
            // 区域树面板隐藏事件
            handleHideTree() {
                this.scrollDestroy()
            },
            // 地图加载完成事件
            handleMapLoaded() {
                let that = this
                // // 默认设置显示图层为市界
                // this.$store.commit('techo/map/currentLayerSet', '基础图层-市界')

                // 默认设置显示图层为市界
                this.$store.commit('techo/map/currentLayerSet', this.currentLayers[this.currentLayers.length - 1])

                // 为要素网格图层绑定鼠标单击事件
                const layers = _.filter(this.mapApi.mapConfig.initLayers, item => {
                    return item.id.indexOf('2') == 0 && item.isBase == true
                })



                // 初始化标注图层
                this.mapApi.mapOper.initTextLayers();

                _.map(layers, item => {
                    this.mapApi.mapOper.activeFeatureClick(item.id, feature => {
                        let code = feature.properties.CODE.length == 4 ? feature.properties.CODE + "00" :
                            feature.properties.CODE;
                        let node = that.$refs.regionTree.getNode(code)
                        that.$store.commit('techo/map/activedNodeSet', node);
                    })
                })

                // 设置楼栋单击事件
                this.mapApi.mapOper.activeFeatureClick("301", feature => {
                    if (that.currentLayer == "基础图层-网格") {
                        that.$store.commit('techo/map/activedHouseLoad', feature.properties.CODE)
                    }
                })

            },
            // 地图缩放完成事件
            handleMapZoom(zoom) {
                if (zoom <= 10) {
                    this.$store.commit('techo/map/currentLayerSet', '基础图层-市界')
                } else if (zoom <= 11.2) {
                    this.$store.commit('techo/map/currentLayerSet', '基础图层-区界')
                } else if (zoom <= 12.8) {
                    this.$store.commit('techo/map/currentLayerSet', '基础图层-街道办')
                } else if (zoom <= 14) {
                    this.$store.commit('techo/map/currentLayerSet', '基础图层-社区')
                } else if (zoom <= 16) {
                    this.$store.commit('techo/map/currentLayerSet', '基础图层-网格')
                }

                // 设置建筑物标注图层是否显示
                if (zoom > 14) {
                    this.isVisibleBuilding && this.mapApi.layerApi.setLayerVisibility("301_Text", true)
                } else {
                    this.mapApi.layerApi.setLayerVisibility("301_Text")
                }
            },
            // 选择图层改变后缩放地图级别
            handleLayerChange(value) {
                this.$store.commit('techo/map/currentLayerSet', value)

                if (value == "基础图层-市界") {
                    this.mapApi.mapOper.setZoom(9)
                } else if (value == "基础图层-区界") {
                    this.mapApi.mapOper.setZoom(10.5)
                } else if (value == "基础图层-街道办") {
                    this.mapApi.mapOper.setZoom(11.8)
                } else if (value == "基础图层-社区") {
                    this.mapApi.mapOper.setZoom(13.5)
                } else if (value == "基础图层-网格") {
                    this.mapApi.mapOper.setZoom(15)
                }
            },
            // 滚动条初始化
            scrollInit() {
                this.BS = new BScroll(document.querySelector(".map-tree-popover"), {
                    mouseWheel: true
                })
            },
            // 滚动条销毁
            scrollDestroy() {
                try {
                    this.BS.destroy()
                } catch (e) {
                    delete this.BS
                    this.BS = null
                }
            },
            // 区域树节点单击事件
            handleTreeNodeClick(data, node) {
                // 设置当前节点
                this.$store.commit('techo/map/activedNodeSet', node);
            },
            // 专题图层按钮单击事件
            handleThemeClick() {
                this.$store.commit('techo/map/activedRightSet', this.activedRight == 'mapTheme' ? '' : 'mapTheme');
            },
            // 选择楼栋信息
            handleSelectHouse(item) {
                this.houseName = item.name
                let node = this.$refs.regionTree.getNode(item.districtCode)
                this.$store.commit('techo/map/activedNodeSet', node);
                this.$store.commit('techo/map/activedHouseSet', _.find(this.autoHouseList, ['id', item.id]))

                this.mapQuery = false
            },
            // 楼栋模糊匹配
            querySearchAsync(queryString, callback) {
                let that = this;

                if (queryString) {
                    let data = service.getBuildingInfoByName({
                        name: queryString,
                        successFunc: data => {
                            that.autoHouseList = _.map(data, item => {
                                return {
                                    id: item.id,
                                    buildingCode: item.buildingCode,
                                    districtCode: item.districtCode,
                                    name: item.name,
                                    address: that.getWgInfoByCode(item.districtCode)
                                }
                            })
                            callback(that.autoHouseList)
                        }
                    })
                }
            },
            // 根据网格编号获取网格信息
            getWgInfoByCode(code) {
                // TODO 后期优化获取
                let that = this
                let address = that.defaultRegion[0].label;

                _.map(that.defaultRegion[0].children, oneItem => {
                    if (oneItem.code != code && code.indexOf(oneItem.code) == 0) {
                        address += oneItem.label;

                        if (oneItem.children) {
                            _.map(oneItem.children, twoItem => {
                                if (twoItem.code != code && code.indexOf(twoItem.code) == 0) {
                                    address += twoItem.label;

                                    if (twoItem.children) {
                                        _.map(twoItem.children, threeItem => {
                                            if (threeItem.code != code && code.indexOf(
                                                    threeItem.code) == 0) {
                                                address += threeItem.label;

                                                if (threeItem.children) {
                                                    _.map(threeItem.children, fourItem => {
                                                        if (fourItem.code != code &&
                                                            code.indexOf(fourItem.code) ==
                                                            0) {
                                                            address += fourItem.label;
                                                        }
                                                    })
                                                }
                                            }
                                        })
                                    }
                                }
                            })
                        }
                    }
                })
                return address;
            },
            // 添加专题图层
            handleThemeAdd(theme) {
                let type = theme.code

                if (this.themeType == "ICON") {
                    this.addThemeLayer(type)
                } else if (this.themeType == "HEXAGON") {
                    this.addHexagonLayer(type)
                } else if (this.themeType == "HEAT") {
                    this.addHeatLayer(type)
                }

            },
            // 移除专题图层
            handleThemeRemove(theme) {
                if (this.themeType == "ICON") {
                    this.mapApi.markerApi.clearMarkers(theme.code)
                } else if (this.themeType == "HEXAGON") {
                    if (this.mapApi.map.getLayer(theme.code + "_HEXAGON")) {
                        this.mapApi.map.removeLayer(theme.code + "_HEXAGON")
                    }
                } else if (this.themeType == "HEAT") {
                    if (this.mapApi.map.getLayer(theme.code + "_HEAT")) {
                        this.mapApi.map.removeLayer(theme.code + "_HEAT")
                        this.mapApi.map.removeSource(theme.code + "_HEAT")
                    }
                }
            },
            // 添加专题点图层
            addThemeLayer(type) {
                let that = this
                themeService[type] && themeService[type]({
                    code: this.currentRegion == null ?
                        constants.REGION.CODE : this.currentRegion.code,
                    successFunc: data => {
                        // console.log('img_' + type)
                        that.mapApi.markerApi.addMarkers(
                            _.map(_.filter(data, item => {
                                return item.x != null
                            }), item => {
                                return {
                                    type: type,
                                    lon: item.x,
                                    lat: item.y,
                                    props: item
                                }
                            }), {
                                ['img_' + type]: themeService['img_' + type]
                            },
                            (event) => {
                                let props = event.features[0].properties
                                let type = event.features[0].source

                                that.dlgVisible = true
                                switch (type) {
                                    // 综治机构
                                    case "zz_organization":
                                        that.configKey = "zzOrg";
                                        that.dlgTitle = "综治机构"
                                        break
                                        // 综治队伍
                                    case "zz_team":
                                        that.configKey = "zzTeam";
                                        that.dlgTitle = "综治队伍"
                                        break
                                        // 群防群治机构
                                    case "qfqz_organization":
                                        that.configKey = "orgMass";
                                        that.dlgTitle = "群防群治机构"
                                        break
                                        // 群防群治组织
                                    case "qfqz_team":
                                        that.configKey = "massTeam";
                                        that.dlgTitle = "群防群治组织"
                                        break
                                        // 综治中心
                                    case "zz_center":
                                        that.configKey = "center";
                                        that.dlgTitle = "综治中心"
                                        break
                                        // 视联中心
                                    case "net_center":
                                        that.configKey = "zzTeam";
                                        that.dlgTitle = "综治机构"
                                        break
                                        // 群防群治机构
                                    case "safe_video":
                                        that.configKey = "safeVideo";
                                        that.dlgTitle = "综治机构"
                                        break
                                        // 网格化建设
                                    case "grid_build":
                                        that.configKey = "gridBuild";
                                        that.dlgTitle = "综治机构"
                                        break
                                        // 楼栋长
                                    case "building_leader":
                                        that.configKey = "buildLeader";
                                        that.dlgTitle = "楼栋长"
                                        break
                                        // 综治领导责任制
                                    case "zz_leader_job":
                                        that.configKey = "leaderDuty";
                                        that.dlgTitle = "综治领导责任制"
                                        break
                                        // 重大案件
                                    case "important_case":
                                        that.configKey = "greatCase";
                                        that.dlgTitle = "综治机构"
                                        break
                                        // 户籍人口
                                    case "household_population":
                                        that.configKey = "housePeople";
                                        that.dlgTitle = "户籍人口"
                                        break
                                        // 流动人口
                                    case "fluid_population":
                                        that.configKey = "flowPeople";
                                        that.dlgTitle = "流动人口"
                                        break
                                        // 留守人员
                                    case "rear_people":
                                        that.configKey = "rearPeople";
                                        that.dlgTitle = "留守人员"
                                        break
                                        // 境外人员
                                    case "overseas_people":
                                        that.configKey = "overseasPeople";
                                        that.dlgTitle = "境外人员"
                                        break
                                        // 出租房
                                    case "rental_housing":
                                        that.configKey = "rentalHouse";
                                        that.dlgTitle = "出租房屋"
                                        break
                                        // 吸毒人员
                                    case "drugster_people":
                                        that.configKey = "drugsterPeople";
                                        that.dlgTitle = "吸毒人员"
                                        break
                                        // 社区矫正人员
                                    case "community_people":
                                        that.configKey = "communityPeople";
                                        that.dlgTitle = "社区矫正人员"
                                        break
                                        // 刑满释放人员
                                    case "released_people":
                                        that.configKey = "releasedPeople";
                                        that.dlgTitle = "刑满释放人员"
                                        break
                                        // 精神病人
                                    case "psychotic_people":
                                        that.configKey = "psychoticPeople";
                                        that.dlgTitle = "精神障碍患者"
                                        break
                                        // 艾滋病人员
                                    case "aids_people":
                                        that.configKey = "aidsPeople";
                                        that.dlgTitle = "艾滋病人员"
                                        break
                                        // TODO 重大青少年
                                    case "important_youngsters":
                                        that.configKey = "zzTeam";
                                        that.dlgTitle = "重大青少年"
                                        break
                                        // 新经济组织
                                    case "economics_organization":
                                        that.configKey = "unPublicEco";
                                        that.dlgTitle = "新经济组织"
                                        break
                                        // 新社会组织
                                    case "society_organization":
                                        that.configKey = "groupSocial";
                                        that.dlgTitle = "新社会组织"
                                        break
                                        // 校园安全
                                    case "school_security":
                                        that.configKey = "schoolInfo";
                                        that.dlgTitle = "校园安全"
                                        break
                                        // 校园案件
                                    case "xxzbi_people":
                                        that.configKey = "schoolRimPeo";
                                        that.dlgTitle = "涉及师生安全案事件"
                                        break
                                        // TODO 
                                    case "refer_teach":
                                        that.configKey = "preferstCase";
                                        that.dlgTitle = "综治机构"
                                        break
                                        // TODO 矛盾纠纷
                                    case "contradiction_resolve":
                                        that.configKey = "zzTeam";
                                        that.dlgTitle = "矛盾纠纷排查化解"
                                        break
                                        // 护路护线
                                    case "roadline_prote":
                                        that.configKey = "lineProte";
                                        that.dlgTitle = "护路护线"
                                        break
                                        // 护路护线案卷
                                    case "lineroad_case":
                                        that.configKey = "lineCase";
                                        that.dlgTitle = "综治机构"
                                        break
                                        // 重大区域排查
                                    case "district_check":
                                        that.configKey = "districtCheck";
                                        that.dlgTitle = "涉及线路案件"
                                        break
                                        // 命案防控
                                    case "mur_case_info":
                                        that.configKey = "caseInfo";
                                        that.dlgTitle = "命案防控"
                                        break
                                        // 命案犯罪嫌疑人
                                    case "mur_suspect":
                                        that.configKey = "suspect";
                                        that.dlgTitle = "命案犯罪嫌疑人"
                                        break
                                        // TODO 命案受害人
                                    case "mur_victim":
                                        that.configKey = "zzTeam";
                                        that.dlgTitle = "命案受害人"
                                        break
                                        // 寄递物流安全管理
                                    case "logistics_safe":
                                        that.configKey = "logisticsSafe";
                                        that.dlgTitle = "寄递物流安全管理"
                                        break
                                    default:
                                        break
                                }

                                that.$nextTick(function () {
                                    that.$refs["details"].data = props;
                                    that.$refs["details"].visible = true;
                                });
                            }
                        )
                    }
                })
            },
            // 添加蜂窝图图层
            addHexagonLayer(type) {
                let that = this
                themeService[type] && themeService[type]({
                    code: this.currentRegion == null ?
                        constants.REGION.CODE : this.currentRegion.code,
                    successFunc: data => {
                        this.mapApi.themeApi.createHexagonLayer({
                            id: type + "_HEXAGON",
                            data: _.map(_.filter(data, item => {
                                return item.x != null
                            }), item => {
                                return {
                                    type: type,
                                    lon: item.x,
                                    lat: item.y,
                                    props: item
                                }
                            })
                        })
                    }
                })
            },
            // 添加热力图层
            addHeatLayer(type) {
                let that = this

                themeService[type] && themeService[type]({
                    code: this.currentRegion == null ?
                        constants.REGION.CODE : this.currentRegion.code,
                    successFunc: data => {
                        let json = {
                            type: "FeatureCollection",
                            features: _.map(_.filter(data, item => {
                                return item.x != null
                            }), item => {
                                return {
                                    type: "Feature",
                                    "geometry": {
                                        "type": "Point",
                                        "coordinates": [item.x, item.y]
                                    }
                                }
                            })
                        }

                        this.mapApi.themeApi.createHeatLayer({
                            id: type + "_HEAT",
                            data: json
                        })
                    }
                })
            }
        },
        watch: {
            // 监听路由 地图专题图层显示
            '$route.params': function (val) {
                if (val.name) {
                    this.$store.commit('techo/map/activedRightSet', 'mapTheme');
                    this.$refs.theme.handleCheckNode([this.$route.params.name]);
                }
            },
            // 监听底图类型变化，修改地图底图
            baseMapType(nValue, oValue) {
                if (nValue == 'vector') {
                    this.mapApi.layerApi.setLayerVisibility('101', true);
                    this.mapApi.layerApi.setLayerVisibility('102', false);
                } else {
                    this.mapApi.layerApi.setLayerVisibility('102', true);
                    this.mapApi.layerApi.setLayerVisibility('101', false);
                }
            },
            // 树选中节点改变
            activedNode(nValue, oValue) {
                if (nValue != null) {
                    // 隐藏下拉框
                    this.mapTree = false;
                    // 设置面板展示
                    this.$store.commit('techo/map/activedLeftSet', nValue.key.length == 14 ? 'mapGridRegion' :
                        'mapRegion');
                } else {
                    this.$store.commit('techo/map/activedLeftSet', '')
                }
            },
            // 选中楼栋改变
            activedHouse(nValue, oValue) {
                let that = this
                if (nValue != null) {
                    this.$store.commit('techo/map/activedLeftSet', 'mapHouse')

                    // TODO buildingCode字段值录入反了，后期修改为districtCode
                    if (this.activedNode == null) {
                        let node = this.$refs.regionTree.getNode(this.activedHouse.buildingCode)
                        this.$store.commit('techo/map/activedNodeSet', node);
                    }
                    // 设置激活要素颜色
                    // that.mapApi.mapOper.setActivedFeature('301', nValue.buildingCode)
                    // this.$nextTick(function () {
                    //     setTimeout(() => {
                    //         debugger
                    //         let features = that.mapApi.mapOper.queryFeatureByCode("301_Text", that.activedHouse
                    //             .districtCode)
                    //         if (features.length > 0) {
                    //             that.mapApi.mapOper.setActivedText("301_Text", features[0].id)
                    //         }
                    //     }, 2000);
                    // })
                } else {
                    // 取消激活标注
                    this.mapApi.mapOper.unsetActivedText("301_Text")
                }

                // 设置选中的房屋为空
                this.$store.commit('techo/map/activedHouseHolderSet', null)
            },
            // TODO 楼栋列表信息,同步在地图中显示楼栋面
            houseList(nValue, oValue) {

            },
            // 监听专题图层面板是否关闭
            activedRight(nValue, oValue) {
                if (nValue == "") {
                    this.$store.commit('techo/map/clearActivedLayer')
                    this.mapApi.markerApi.clearMarkers()
                }
            },
            // 当前区域改变触发事件
            currentRegion(nValue, oValue) {
                let that = this
                let layer, layerId, code, zoom;

                if (nValue == null || nValue.code == constants.REGION.CODE) {
                    layer = '基础图层-市界'
                    layerId = "201"
                    code = constants.REGION.CODE
                    zoom = 9
                } else if (nValue.code.length == 6) {
                    layer = '基础图层-区界'
                    layerId = "202"
                    code = nValue.code
                    zoom = 10.5
                } else if (nValue.code.length == 9) {
                    layer = '基础图层-街道办'
                    layerId = "203"
                    code = nValue.code
                    zoom = 11.8
                } else if (nValue.code.length == 12) {
                    layer = '基础图层-社区'
                    layerId = "204"
                    code = nValue.code
                    zoom = 13.5
                } else if (nValue.code.length == 14) {
                    layer = '基础图层-网格'
                    layerId = "205"
                    code = nValue.code
                    zoom = 15
                }

                // 如果专题图层界面打开,重新根据新区域获取专题数据
                if (this.activedRight == 'mapTheme' && this.activedLayers.length > 0) {
                    _.map(this.activedLayers, item => {
                        this.mapApi.markerApi.clearMarkers(item.code)
                        this.addThemeLayer(item.code)
                    })
                }

                // 切换显示图层
                this.$store.commit('techo/map/currentLayerSet', layer)
                // 获取区域中心点
                const center = _.find(this.centerData[layer], item => {
                    return item.properties.CODE == code
                }).geometry.coordinates
                // 设置地图移动到选中区域
                this.mapApi.mapOper.setCenterAndZoom(center, zoom)

                // 设置当前选中区域高亮
                setTimeout(() => {
                    if (that.currentRegion != null) {
                        let features = mapApi.map.queryRenderedFeatures({
                            layers: [layerId],
                            filter: ["==", "CODE", this.currentRegion.code]
                        })

                        if (features.length > 0) {
                            that.mapApi.mapOper.setActivedFeature(layerId, features[0].id)
                        }
                    }
                }, 500);

                // 取消选中区域高亮
                if (nValue == null & this.mapApi.mapOper.activedFeature != null) {
                    this.mapApi.mapOper.unsetActivedFeature(this.mapApi.mapOper.activedLayer, this.mapApi.mapOper.activedFeature)
                }
            },
            // 当前显示图层
            currentLayer(nValue, oValue) {
                this.rdValue = nValue
                const layers = _.filter(this.mapApi.mapConfig.initLayers, item => {
                    return item.id.indexOf('2') == 0
                })

                // 设置图层显隐
                _.map(layers, item => {
                    if (item.name.indexOf(nValue) == 0) {
                        this.mapApi.layerApi.setLayerVisibility(item.id, true);
                    } else {
                        this.mapApi.layerApi.setLayerVisibility(item.id, false);
                    }
                })

                // 设置图层要素可高亮
                if (oValue != "") {
                    let oId = _.find(layers, ['name', oValue]).id
                    this.mapApi.mapOper.deactiveFeatureHight(oId);
                }

                let nId = _.find(layers, ['name', nValue]).id
                this.mapApi.mapOper.activeFeatureHight(nId);
            },
            // 专题图层选择操作
            activedLayers(nValue, oValue) {
                if (nValue.length > oValue.length) {
                    // let data = 
                }
            },
            // 修改楼栋图层显隐
            isVisibleBuilding(nValue, oValue) {
                if (nValue == false) {
                    this.mapApi.map.removeLayer("301");
                } else {
                    this.mapApi.map.addLayer(_.find(this.mapApi.mapOper.layers, ['id', "301"]));
                }

                this.mapApi.layerApi.setLayerVisibility("301_Text", nValue);
            },
            themeType(nValue, oValue) {
                if (nValue == "ICON") {
                    this.isVisibleBuilding = true
                } else if (nValue == "HEXAGON" || nValue == "HEAT") {
                    this.isVisibleBuilding = false
                }
            },
            // 数据专题与资源专题切换
            themeClass(nValue, oValue) {
                if (this.activedRight == '') {
                    return
                }

                if (nValue == "theme_resource") {
                    this.$store.commit('techo/map/activedRightSet', 'mapTheme');
                } else {
                    this.$store.commit('techo/map/activedRightSet', 'mapData');
                }
            }
        }
    }
</script>

<style lang="scss">
    @import './styles/custom_map.scss';
