<template>
    <div class="map-theme-switch-wrapper">
        <div @click="type='theme_resource'" class="map-theme-switch-item" :class="themeClass=='theme_resource'?'map-theme-switch-item-active':''">
            <span>资源专题</span>
        </div>
        <div @click="type='theme_data'" class="map-theme-switch-item" :class="themeClass=='theme_data'?'map-theme-switch-item-active':''">
            <span>数据专题</span>
        </div>
    </div>
</template>

<script>
    import {
        mapState,
        mapActions
    } from 'vuex'
    export default {
        data() {
            return {
                type: 'theme_resource',
                imgOne: require("./images/1.png"),
                imgTwo: require("./images/2.png")
            }
        },
        computed: {
            ...mapState('techo/map', [
                'themeClass'
            ])
        },
        watch: {
            // 专题类型
            type(nValue, oValue) {
                this.$store.commit('techo/map/themeClassSet', nValue)
            },
            themeClass(nValue,oValue){
                this.type = nValue
            }
        }
    }
</script>

