<template>
    <el-dialog class="map-list-wrapper" :title="title+'列表'" :visible.sync="visible" width="80%" append-to-body @opened="handleOpen">
        <el-tabs class="map-list__body-tabs" type="card" :value="currentTab" @tab-click="handleTabClick">
            <el-tab-pane v-for="(tab, index) in tabs" :key="index" :label="tab.name" :name="tab.name" :class="[tabIndex==index?'el-tabs__item-active':'']">
                <span slot="label">
                    <!-- <techo-icon style="margin-right:10px;" :name="page.meta.icon || 'bars'" /> -->
                    {{tab.name}}</span>
            </el-tab-pane>
        </el-tabs>
        <div class="map-list__body-table map-list__body-tab-table">
            <el-table ref="table" size="medium" :data="data" border highlight-current-row tooltip-effect="dark" height="100%"
                style="width: 100%" header-cell-class-name="techo-list__body-table-header">
                <el-table-column type="index" label="序号" width="50" align="center">
                </el-table-column>
                <el-table-column v-for="(value, key, index) in tabs.length>0?tabs[tabIndex].header:[]" :key="index"
                    :label="key" :prop="value" align="center" show-overflow-tooltip></el-table-column>
                <el-table-column label="操作" fixed="right" align="center" width="80">
                    <template slot-scope="scope">
                        <el-button size="mini" class="map-list__body-table-button-view" @click="handleView(scope.$index,scope.row)">查看</el-button>
                    </template>
                </el-table-column>
            </el-table>
        </div>
        <div class="map-list__body-pager" style="float:right;">
            <el-pagination ref="pager" @size-change="handlePagerChange" @current-change="handlePagerChange" layout="total, prev, pager, next, jumper"
                :total="recordCount">
            </el-pagination>
        </div>
        <techo-details v-if="dlgVisible" ref="details" :configKey="configKey" :height="'60%'"></techo-details>
    </el-dialog>
</template>

<script>
    export default {
        props: {

        },
        data() {
            return {
                BS: null,
                // 组件可见性
                visible: false,
                // 详情信息框可见性
                dlgVisible: false,
                // 页面标题
                title: "数据",
                tabs: [],
                currentTab: '',
                tabIndex: 0,
                data: [],
                recordCount: 0,
                // 详情配置项
                configKey: ''
            }
        },
        methods: {
            handleOpen() {
                this.scrollInit();
            },
            // 滚动条初始化
            scrollInit() {
                this.BS = new BScroll(this.$el.querySelector(".techo-list__body-table"), {
                    mouseWheel: true
                })
            },
            // 滚动条销毁
            scrollDestroy() {
                try {
                    this.BS.destroy()
                } catch (e) {
                    delete this.BS
                    this.BS = null
                }
            },
            // 分页查询
            handlePagerChange() {
                let page = this.$refs['pager'] ? this.$refs['pager'].internalCurrentPage : 1;
                this.$emit('onPageChange', this.currentTab, page)
            },
            handleTabClick(tab) {
                this.tabIndex = tab.index;
                this.currentTab = this.tabs[tab.index].name
            },
            // 查看记录详细信息
            handleView(index, row) {
                switch (this.currentTab) {
                    case "吸毒人员":
                        this.configKey = "drugsterPeople"
                        break
                    case "社区矫正人员":
                        this.configKey = "communityPeople"
                        break
                    case "刑满释放人员":
                        this.configKey = "releasedPeople"
                        break
                    case "精神障碍患者":
                        this.configKey = "psychoticPeople"
                        break
                    case "艾滋病人员":
                        this.configKey = "aidsPeople"
                        break
                    default:
                        break
                }

                this.dlgVisible = true;
                this.$nextTick(function () {
                    this.$refs["details"].data = row;
                    this.$refs["details"].visible = true;
                });
                event.stopPropagation();
            }
        },
        beforeDestroy() {
            this.scrollDestroy()
        },
        watch: {
            currentTab(nValue, oValue) {
                this.handlePagerChange()
            }
        }
    }
</script>

