<template>
    <el-dialog class="map-list-wrapper" :title="title+'列表'" :visible.sync="visible" width="80%" append-to-body @opened="handleOpen">
        <div class="map-list__body-table" style="height: calc(100% - 40px);">
            <el-table ref="table" size="medium" :data="data" border highlight-current-row tooltip-effect="dark" height="100%"
                style="width: 100%" header-cell-class-name="techo-list__body-table-header">
                <el-table-column type="index" label="序号" width="50" align="center">
                </el-table-column>
                <el-table-column v-for="(value, key, index) in header" :key="index" :label="key" :prop="value" align="center"
                    show-overflow-tooltip></el-table-column>
                <el-table-column label="操作" fixed="right" align="center" width="80">
                    <template slot-scope="scope">
                        <el-button size="mini" class="map-list__body-table-button-view" @click="handleView(scope.$index,scope.row)">查看</el-button>
                    </template>
                </el-table-column>
            </el-table>
        </div>
        <div class="map-list__body-pager" style="float:right;">
            <el-pagination ref="pager" @size-change="handlePagerChange" @current-change="handlePagerChange" layout="total, prev, pager, next, jumper"
                :total="recordCount">
            </el-pagination>
        </div>
        <techo-details v-if="dlgVisible" ref="details" mapModule :title="dlgTitle" :configKey="configKey" :height="'60%'"></techo-details>
    </el-dialog>
</template>

<script>
    export default {
        props: {

        },
        data() {
            return {
                BS: null,
                // 组件可见性
                visible: false,
                // 详情信息框可见性
                dlgVisible: false,
                // 页面标题
                title: "数据",
                // 列表表头
                header: [],
                // 列表数据
                data: [],
                // 列表数据总数
                recordCount: 0,
                // 详情配置项
                configKey: '',
                // 详情对话框标题
                dlgTitle: ''
            }
        },
        methods: {
            handleOpen() {
                this.scrollInit();
            },
            // 滚动条初始化
            scrollInit() {
                this.BS = new BScroll(this.$el.querySelector(".techo-list__body-table"), {
                    mouseWheel: true
                })
            },
            // 滚动条销毁
            scrollDestroy() {
                try {
                    this.BS.destroy()
                } catch (e) {
                    delete this.BS
                    this.BS = null
                }
            },
            // 分页查询
            handlePagerChange() {
                let page = this.$refs['pager'].internalCurrentPage ? this.$refs['pager'].internalCurrentPage : 1;
                this.$emit('onPageChange', this.title, page)
            },
            // 查看记录详细信息
            handleView(index, row) {
                switch (this.title) {
                    case "户籍人口":
                        this.configKey = "housePeople"
                        this.dlgTitle = "户籍人口"
                        break
                    case "流动人口":
                        this.configKey = "flowPeople"
                        this.dlgTitle = "流动人口"
                        break
                    case "重点青少年":
                        this.configKey = "flowPeople"
                        this.dlgTitle = "重点青少年"
                        break
                    case "留守人员":
                        this.configKey = "rearPeople"
                        this.dlgTitle = "留守人员"
                        break
                    case "境外人员":
                        this.configKey = "overseasPeople"
                        this.dlgTitle = "境外人员"
                        break
                    case "出租房":
                        this.configKey = "rentalHouse"
                        this.dlgTitle = "出租房屋"
                        break
                    case "企业":
                        this.configKey = "rentalHouse"
                        this.dlgTitle = "新经济组织"
                        break
                    case "场所":
                        this.configKey = "rentalHouse"
                        this.dlgTitle = "新社会组织"
                        break
                    case "学校":
                        this.configKey = "rentalHouse"
                        this.dlgTitle = "校园安全"
                        break
                    default:
                        break
                }

                this.dlgVisible = true;
                this.$nextTick(function () {
                    // this.$refs["details"].title = this.dlgTitle;
                    this.$refs["details"].data = row;
                    this.$refs["details"].visible = true;
                });
                event.stopPropagation();
            }
        },
        beforeDestroy() {
            this.scrollDestroy()
        }
    }
</script>

