<template>
    <el-dialog custom-class="techo-region-list" class="techo-details-wrapper " :title="title" :visible.sync="visible"
        width="50%" append-to-body @opened="handleOpen">
            <div class="techo-details__body techo-list__body-table">
                <el-table ref="table" size="medium" :data="data" border highlight-current-row tooltip-effect="dark"
                    stripe height="100%" style="width: 100%" header-cell-class-name="techo-list__body-table-header">
                    <el-table-column type="index" label="序号" width="50" align="center">
                    </el-table-column>
                    <el-table-column v-for="(value, key, index) in header" :key="index" :label="key" :prop="value"
                        align="center" show-overflow-tooltip></el-table-column>
                    <!-- <el-table-column label="操作" fixed="right" align="center" width="130">
                    <template slot-scope="scope">
                        <el-button size="mini" class="techo-list__body-table-button-view" @click="handleView(scope.$index,scope.row)">查看</el-button>
                    </template>
                </el-table-column> -->
                </el-table>
            </div>
    </el-dialog>
</template>

<script>
    import BScroll from 'better-scroll'

    export default {
        props: {
            // 页面标题
            title: {
                type: String,
                default: "区域列表"
            },
            // 列表表头
            header: {
                type: Object
            },
        },
        data() {
            return {
                BS: null,
                // 组件可见性
                visible: false,
                data: []
            }
        },
        methods: {
            handleOpen(){
                this.scrollInit();
            },
            // 滚动条初始化
            scrollInit() {
                this.BS = new BScroll(this.$el.querySelector(".techo-list__body-table"), {
                    mouseWheel: true
                    // 如果你愿意可以打开显示滚动条
                    // scrollbar: {
                    //   fade: true,
                    //   interactive: false
                    // }
                })
            },
            // 滚动条销毁
            scrollDestroy() {
                try {
                    this.BS.destroy()
                } catch (e) {
                    delete this.BS
                    this.BS = null
                }
            },
        },
        beforeDestroy() {
            this.scrollDestroy()
        }
    }
</script>

