<template>
    <div class="map-grid-table-wrapper">
        <div class="map-grid-table-query">
            <el-input size="samll" placeholder="请输入楼栋名称" v-model="name" class="map-grid-table-input">
                <el-button slot="append" icon="el-icon-search" @click="handleQuery"></el-button>
            </el-input>
        </div>
        <div class="map-grid-table-house">
            <el-table ref="table" size="mini" :data="houseList.list" highlight-current-row tooltip-effect="dark" stripe
                style="width: 100%" header-cell-class-name="map-grid-table-header" cell-class-name="map-grid-table-cell"
                @row-click="handleRowClick">
                <el-table-column type="index" label="序号" width="50" align="center"></el-table-column>
                <el-table-column label="楼栋编号" prop="buildingCode" align="center" show-overflow-tooltip></el-table-column>
                <el-table-column label="楼栋名称" prop="name" align="center" show-overflow-tooltip></el-table-column>
                <el-table-column label="操作" align="center" width="70">
                    <template slot-scope="scope">
                        <el-button size="mini" class="map-grid-table-button-view" @click="handleView(scope.$index,scope.row)">查看</el-button>
                    </template>
                </el-table-column>
            </el-table>
        </div>
        <el-pagination class="map-grid-table-pager" ref="pager" @size-change="handleQuery" @current-change="handleQuery"
            layout="total, prev, pager, next" :total="houseList.totalCount"></el-pagination>
    </div>
</template>
<script>
    import {
        mapState
    } from 'vuex'
    import turfCentroid from '@turf/centroid';
    import BScroll from 'better-scroll'

    export default {
        data() {
            return {
                name: ''
            }
        },
        computed: {
            ...mapState('techo/map', [
                'activedLeft', 'houseList'
            ])
        },
        created() {
            this.handleQuery();
        },
        mounted() {
            this.scrollInit()
        },
        methods: {
            // 查询房屋列表
            handleQuery() {
                let pageInfo = this.getPageInfo();
                this.$store.commit('techo/map/houseListLoad', {
                    name: this.name,
                    page: pageInfo.page,
                    pageSize: pageInfo.pageSize
                })
            },
            // 获取分页信息
            getPageInfo() {
                let page, pageSize, pageInfo;

                // TODO 缓存分页信息
                if (this.$refs['pager'] == undefined) {
                    page = 1;
                    pageSize = 10;
                } else {
                    page = this.$refs['pager'].internalCurrentPage;
                    pageSize = this.$refs['pager'].internalPageSize;
                }

                return {
                    page,
                    pageSize
                };
            },
            // 单击行定位到建筑
            handleRowClick(row) {
                const build = mapApi.map.querySourceFeatures("301_Text", {
                    filter: ["==", "CODE", row.buildingCode]
                })

                // 设置激活的楼栋
                this.$store.commit('techo/map/activedHouseSet', row)

                if (build.length > 0) {
                    const center = turfCentroid(build[0].geometry)
                    mapApi.mapOper.setCenterAndZoom(center.geometry.coordinates, 17)
                    mapApi.mapOper.setActivedText("301_Text", build[0].id)
                    // mapApi.map.setFilter("302", ["==", "CODE", row.buildingCode]);
                }
            },
            // 查看选中的楼栋
            handleView(index, row) {
                this.$store.commit('techo/map/activedHouseSet', row)
            },
            // 滚动条初始化
            scrollInit() {
                this.BS = new BScroll(this.$el.querySelector(".map-grid-table-house"), {
                    mouseWheel: true
                })
            },
            // 滚动条销毁
            scrollDestroy() {
                try {
                    this.BS.destroy()
                } catch (e) {
                    delete this.BS
                    this.BS = null
                }
            },
        },
        beforeDestroy() {
            this.scrollDestroy()
        }
    }
</script>
<style></style>