<template>
    <div class="map-region-wg-content">
        <el-row type="flex" justify="space-around">
            <el-col class="map-region-wg-item" @click.native="handleQueryList(index)" v-for="(item,index) in baseInfo"
                :span="6" :key="index">
                <img :src="item.img">
                <span>{{item.name}}</span>
            </el-col>
            <el-col class="map-region-wg-item" @click.native="handleQueryList(4)" :span="5">
                <img :src="imgWGY">
                <span>网格长</span>
            </el-col>
        </el-row>
        <el-row class="map-region-wg-count" type="flex" justify="space-around">
            <el-col class="map-region-wg-item" @click.native="handleQueryList(index)" v-for="(item,index) in baseInfo"
                @click="handleQueryList(item)" :span="6" :key="index">
                <span class="span-color-red">{{item.count}}</span>
                <span class="span-color-red span-unit">个</span>
            </el-col>
            <el-col class="map-region-wg-item" @click.native="handleQueryList(4)" :span="5" @click="handleQueryList(item)">
                <span class="span-color-red">{{wgyCount}}</span>
                <span class="span-color-red span-unit">人</span>
            </el-col>
        </el-row>
        <vListOfRegionData v-if="regionVisible" ref="listRegion" :header="regionHeader"></vListOfRegionData>
        <vListOfWgyData @onPageChange="handlePageChange" v-if="wgyVisible" ref="listWgy" :header="wgyHeader"></vListOfWgyData>
    </div>
</template>

<script>
    import {
        mapState,
        mapGetters
    } from 'vuex'
    import vListOfRegionData from './list-region-data.vue';
    import vListOfWgyData from './list-wgy-data.vue';
    import service from "../../../map_service"

    export default {
        components: {
            vListOfRegionData,
            vListOfWgyData
        },
        data() {
            return {
                imgCounty: require("../images/cont_icon_grid.png"),
                imgStreet: require("../images/cont_icon_towns.png"),
                imgCommunity: require("../images/cont_icon_community.png"),
                imgGrid: require("../images/cont_icon_grid.png"),
                imgWGY: require("../images/cont_icon_personnel.png"),
                wgyCount: 0,
                // 详情页面是否显示
                regionVisible: false,
                wgyVisible: false,
                regionHeader: {
                    区划代码: "code",
                    区划名称: "label"
                },
                wgyHeader: {
                    姓名: "name",
                    联系电话: "tellphone",
                    负责区域: "region"
                }
            }
        },
        computed: {
            ...mapGetters('techo/map', ['currentRegion']),
            baseInfo() {
                const infos = [];
                if (this.currentRegion.children && this.currentRegion.children.length > 0) {
                    if (this.currentRegion.level == 1) {
                        infos.push({
                            name: "县(区)",
                            count: this.currentRegion.children.length,
                            img: this.imgCounty
                        });
                        infos.push({
                            name: "乡镇(街道)",
                            count: _.sum(_.map(this.currentRegion.children, item => {
                                return item.children.length
                            })),
                            img: this.imgStreet
                        });
                        infos.push({
                            name: "村(社区)",
                            count: _.sum(_.map(this.currentRegion.children, item => {
                                return _.sum(_.map(item.children, subitem => {
                                    return subitem.children.length
                                }))
                            })),
                            img: this.imgCommunity
                        });
                        infos.push({
                            name: "网格",
                            count: _.sum(_.map(this.currentRegion.children, item => {
                                return _.sum(_.map(item.children, subitem => {
                                    return _.sum(_.map(subitem.children, titem => {
                                        return titem.children.length
                                    }))
                                }))
                            })),
                            img: this.imgGrid
                        });
                    } else if (this.currentRegion.level == 2) {
                        infos.push({
                            name: "乡镇(街道)",
                            count: this.currentRegion.children.length,
                            img: this.imgStreet
                        });
                        infos.push({
                            name: "村(社区)",
                            count: _.sum(_.map(this.currentRegion.children, item => {
                                return item.children.length
                            })),
                            img: this.imgCommunity
                        });
                        infos.push({
                            name: "网格",
                            count: _.sum(_.map(this.currentRegion.children, item => {
                                return _.sum(_.map(item.children, subitem => {
                                    return subitem.children.length
                                }))
                            })),
                            img: this.imgGrid
                        });
                    } else if (this.currentRegion.level == 3) {
                        infos.push({
                            name: "村(社区)",
                            count: this.currentRegion.children.length,
                            img: this.imgCommunity
                        });
                        infos.push({
                            name: "网格",
                            count: _.sum(_.map(this.currentRegion.children, item => {
                                return item.children.length
                            })),
                            img: this.imgGrid
                        });
                    } else if (this.currentRegion.level == 4) {
                        infos.push({
                            name: "网格",
                            count: this.currentRegion.children.length,
                            img: this.imgGrid
                        });
                    }
                }

                return infos;
            }
        },
        created() {
            this.handleQueryWgy();
        },
        methods: {
            // 查询网格长数量
            handleQueryWgy() {
                let that = this;
                service.getGridCountByCode({
                    code: this.currentRegion.code,
                    successFunc: (data) => {
                        that.wgyCount = data.totalCount;
                    }
                })
            },
            // 查询网格长列表
            handleQueryWgyList(page) {
                let that = this;
                page = page ? page : 1;

                service.getGridCountByCode({
                    code: this.currentRegion.code,
                    page: 1,
                    pageSize: 10,
                    successFunc: (data) => {
                        that.$refs["listWgy"].data = data.list;
                        that.$refs["listWgy"].recordCount = data.totalCount;
                        that.$refs["listWgy"].visible = true;
                    }
                })
            },
            // 分页查询事件
            handlePageChange(page) {
                this.handleQueryWgyList(page)
            },
            // 查询数量列表信息
            handleQueryList(index) {
                if (index == 0) {
                    this.regionVisible = true
                    this.$nextTick(function () {
                        this.$refs["listRegion"].data = _.orderBy(this.currentRegion.children, 'code');
                        this.$refs["listRegion"].visible = true;
                    });
                } else if (index == 1) {
                    this.regionVisible = true
                    this.$nextTick(function () {
                        this.$refs["listRegion"].data = _.orderBy(_.flatten(_.map(this.currentRegion.children,
                            'children')), 'code', 'asc');
                        this.$refs["listRegion"].visible = true;
                    });
                } else if (index == 2) {
                    this.regionVisible = true
                    this.$nextTick(function () {
                        this.$refs["listRegion"].data = _.orderBy(_.flatten(_.map(_.flatten(_.map(this.currentRegion
                            .children,
                            'children')), 'children')), 'code', 'asc');
                        this.$refs["listRegion"].visible = true;
                    });
                } else if (index == 3) {
                    this.regionVisible = true
                    this.$nextTick(function () {
                        this.$refs["listRegion"].data = _.orderBy(_.flatten(_.map(_.flatten(_.map(_.flatten(_.map(
                            this.currentRegion
                            .children,
                            'children')), 'children')), 'children')), 'code', 'asc');
                        this.$refs["listRegion"].visible = true;
                    });
                } else if (index == 4) {
                    this.wgyVisible = true
                    this.$nextTick(function () {
                        this.handleQueryWgyList();
                    });
                }
            }
        },
        watch: {
            currentRegion(nValue, oValue) {
                this.handleQueryWgy()
            }
        }

    }
</script>

