<template>
    <div class="map-house-wrapper">
        <div class="map-house-header">
            <img src="">
            <span>{{activedHouse.name}}</span>
            <span class="map-house-header-tool">
                <span @click="handleUp">
                    <techo-icon class="map-house-header-tool-icon" name="reply" alt="返回"></techo-icon>
                </span>
                <img class="map-house-header-tool-img" @click="handleClose" :src="imgDelete">
            </span>
        </div>
        <div class="map-house-content">
            <el-row type="flex" justify="space-around">
                <el-col v-for="(item,index) in statisInfo" :span="6" :key="index">
                    <img :src="item.img">
                    <span>{{item.name}}</span>
                </el-col>
            </el-row>
            <el-row class="map-house-count" type="flex" justify="space-around">
                <el-col v-for="(item,index) in statisInfo" :span="6" :key="index">
                    <span class="span-color-orange">{{item.count}}</span>
                </el-col>
            </el-row>
        </div>
        <div class="map-house-info">
            <div class="map-house-info-name">
                <span class="map-house-info-name-tab" :class="[tabIndex==0?'map-house-info-name-tab-active':'']" @click="tabIndex=0">基本信息</span>
                <span class="map-house-info-name-tab" :class="[tabIndex==1?'map-house-info-name-tab-active':'']" @click="tabIndex=1">楼栋结构</span>
            </div>
            <vHouseFrame v-if="tabIndex==1"></vHouseFrame>
            <div v-if="tabIndex==0" class="map-house-info-base">
                <el-row type="flex" justify="space-around">
                    <el-col :span="6" class="column-title">
                        楼栋名称
                    </el-col>
                    <el-col :span="18">
                        {{activedHouse.name}}
                    </el-col>
                </el-row>
                <el-row type="flex" justify="space-around">
                    <el-col :span="6" class="column-title">
                        所在网格
                    </el-col>
                    <el-col :span="18">
                        {{house_grid}}
                    </el-col>
                </el-row>
                <el-row type="flex" justify="space-around">
                    <el-col :span="6" class="column-title">
                        单元数
                    </el-col>
                    <el-col :span="18">
                        {{builderInfo.elementsCount}}
                    </el-col>
                </el-row>
                <el-row type="flex" justify="space-around">
                    <el-col :span="6" class="column-title">
                        层数
                    </el-col>
                    <el-col :span="18">
                        {{builderInfo.pliesCount}}
                    </el-col>
                </el-row>
                <el-row type="flex" justify="space-around">
                    <el-col :span="6" class="column-title">
                        建筑面积
                    </el-col>
                    <el-col :span="18">
                        {{builderInfo.coveredArea}}
                    </el-col>
                </el-row>
                <el-row type="flex" justify="space-around">
                    <el-col :span="6" class="column-title">
                        楼栋户数
                    </el-col>
                    <el-col :span="18">
                        {{builderInfo.buildingHouseCount}}
                    </el-col>
                </el-row>
                <el-row type="flex" justify="space-around">
                    <el-col :span="6" class="column-title">
                        楼栋人数
                    </el-col>
                    <el-col :span="18">
                        {{builderInfo.buildingPeopleCount}}
                    </el-col>
                </el-row>
                <el-row type="flex" justify="space-around">
                    <el-col :span="6" class="column-title">
                        楼栋长姓名
                    </el-col>
                    <el-col :span="18">
                        {{builderInfo.name}}
                    </el-col>
                </el-row>
                <el-row type="flex" justify="space-around">
                    <el-col :span="6" class="column-title">
                        出生日期
                    </el-col>
                    <el-col :span="18">
                        {{builderInfo.name}}
                    </el-col>
                </el-row>
                <el-row type="flex" justify="space-around">
                    <el-col :span="6" class="column-title">
                        性别
                    </el-col>
                    <el-col :span="18">
                        {{builderInfo.dicMap && builderInfo.dicMap.sex}}
                    </el-col>
                </el-row>
                <el-row type="flex" justify="space-around">
                    <el-col :span="6" class="column-title">
                        政治面貌
                    </el-col>
                    <el-col :span="18">
                        {{builderInfo.dicMap && builderInfo.dicMap.politicsStatus}}
                    </el-col>
                </el-row>
                <el-row type="flex" justify="space-around">
                    <el-col :span="6" class="column-title">
                        学历
                    </el-col>
                    <el-col :span="18">
                        {{builderInfo.dicMap && builderInfo.dicMap.education}}
                    </el-col>
                </el-row>
                <el-row type="flex" justify="space-around">
                    <el-col :span="6" class="column-title">
                        手机号码
                    </el-col>
                    <el-col :span="18">
                        {{builderInfo.phoneNumber}}
                    </el-col>
                </el-row>
                <el-row type="flex" justify="space-around">
                    <el-col :span="6" class="column-title">
                        所在地祥址
                    </el-col>
                    <el-col :span="18">
                        {{builderInfo.localityDetail}}
                    </el-col>
                </el-row>
            </div>
        </div>
    </div>
</template>

<script>
    import {
        mapState,
        mapGetters
    } from 'vuex'
    import vHouseFrame from "./components/house-frame.vue";
    import service from '../../map_service';

    export default {
        components: {
            vHouseFrame
        },
        computed: {
            ...mapState('techo/map', ['activedNode', 'activedHouse']),
            ...mapGetters('techo/map', ['currentRegion']),
            baseInfo() {
                return [{
                    img: this.imgRenYuan,
                    name: '人员',
                    count: 0
                }, {
                    img: this.imgQiYe,
                    name: '企业',
                    count: 0
                }, {
                    img: this.imgChangSuo,
                    name: '场所',
                    count: 0
                }, {
                    img: this.imgXueXiao,
                    name: '学校',
                    count: 0
                }]
            },
            house_grid() {
                return this.activedNode.parent.parent.parent.data.label + this.activedNode.parent.parent.data.label +
                    this.activedNode.parent.data.label + this.currentRegion
                    .label
            }
        },
        data() {
            return {
                tabIndex: 0,
                imgRenYuan: require("./images/人员.png"),
                imgQiYe: require("./images/企业.png"),
                imgChangSuo: require("./images/场所.png"),
                imgXueXiao: require("./images/学校.png"),
                imgDelete: require("./images/cont_eject_delete.png"),
                statisInfo: [],
                // 楼栋长信息
                builderInfo: {}
            }
        },
        created() {
            this.handleQueryStatis()
            this.handleQueryBuilder()
        },
        methods: {
            handleClose() {
                this.$store.commit('techo/map/activedHouseSet', null);
                this.$store.commit('techo/map/activedNodeSet', null)
            },
            // 返回上级节点页面
            handleUp() {
                this.$store.commit('techo/map/activedLeftSet', 'mapGridRegion')
            },
            // 获取楼栋统计信息
            handleQueryStatis() {
                let that = this
                service.getHouseStatisByBuildingCode({
                    code: this.activedHouse.buildingCode,
                    successFunc: data => {
                        that.statisInfo = []
                        that.statisInfo.push({
                            img: that.imgRenYuan,
                            name: "人员",
                            count: data.peopleCount
                        });
                        that.statisInfo.push({
                            img: that.imgQiYe,
                            name: "企业",
                            count: data.companyCount
                        });
                        that.statisInfo.push({
                            img: that.imgChangSuo,
                            name: "场所",
                            count: data.placeCount
                        });
                        that.statisInfo.push({
                            img: that.imgXueXiao,
                            name: "学校",
                            count: data.schoolCount
                        });
                    }
                })
            },
            // 获取楼栋长信息
            handleQueryBuilder() {
                let that = this

                service.getBuilderInfoById({
                    id: this.activedHouse.id,
                    successFunc: data => {
                        that.builderInfo = data;
                    }
                })
            }
        },
        beforeDestroy(){
            this.$store.commit('techo/map/activedHouseSet', null);
        }
    }
</script>

<style lang="scss">
    @import './styles/house_data.scss';
</style>