<template>
    <div class="map-house-user-wrapper">
        <div class="map-house-user-header">
            <img src="">
            <span>房屋户人口</span>
            <span class="map-house-user-header-tool">
                <img class="map-house-user-header-tool-img" @click="handleClose" :src="imgDelete">
            </span>
        </div>
        <div class="map-house-user-content">
            <el-row type="flex" justify="center">
                <el-col class="el-col-title" :span="6">
                    <span>序号</span>
                </el-col>
                <el-col class="el-col-title" :span="9">
                    <span>姓名</span>
                </el-col>
                <el-col class="el-col-title" :span="9">
                    <span>性别</span>
                </el-col>
            </el-row>
            <el-row @click.native="handleView(item)" class="map-house-user-item" v-for="(item,index) in userList" :key="index"
                type="flex" justify="space-around">
                <el-col :span="6">
                    <span class="span-color-orange">{{index+1}}</span>
                </el-col>
                <el-col :span="9">
                    <span class="span-color-orange">{{item.name}}</span>
                </el-col>
                <el-col :span="9">
                    <span class="span-color-orange">{{item.dicMap.sex}}</span>
                </el-col>
            </el-row>
        </div>
        <techo-details v-if="dlgVisible" ref="details" :configKey="configKey" :height="'60%'"></techo-details>
    </div>
</template>

<script>
    import {
        mapState,
        mapGetters
    } from 'vuex'
    import service from '../../../map_service';
    export default {
        data() {
            return {
                imgDelete: require("../images/cont_eject_delete.png"),
                // 用户信息列表
                userList: [],
                // 详情信息框可见性
                dlgVisible: false,
                // 详情配置项
                configKey: ''
            }
        },
        computed: {
            ...mapState('techo/map', ['activedNode', 'activedHouse', 'activedHouseHolder']),
        },
        methods: {
            // 查看人口详情
            handleView(item) {
                this.configKey = "housePeople"
                this.dlgVisible = true;
                this.$nextTick(function () {
                    this.$refs["details"].data = item;
                    this.$refs["details"].visible = true;
                });
            },
            // 关闭快速查看用户面板
            handleClose() {
                this.$store.commit('techo/map/activedHouseHolderSet', null)
            }
        },
        watch: {
            activedHouseHolder(nValue, oValue) {
                let that = this
                if (nValue) {
                    service.getUserInfoByHouseId({
                        id: nValue.id,
                        successFunc: data => {
                            that.userList = data
                        }
                    })
                }
            }
        }
    }
</script>

