<template>
    <el-dialog custom-class="techo-region-list" class="techo-details-wrapper " :title="title" :visible.sync="visible"
        width="50%" append-to-body @opened="handleOpen">
        <div class="techo-details__body techo-list__body-table" style="height: calc(100% - 40px);">
            <el-table ref="table" size="medium" :data="data" border highlight-current-row tooltip-effect="dark" stripe
                height="100%" style="width: 100%" header-cell-class-name="techo-list__body-table-header">
                <el-table-column type="index" label="序号" width="50" align="center">
                </el-table-column>
                <el-table-column v-for="(value, key, index) in header" :key="index" :label="key" :prop="value" align="center"
                    show-overflow-tooltip></el-table-column>
                <!-- <el-table-column label="操作" fixed="right" align="center" width="130">
                    <template slot-scope="scope">
                        <el-button size="mini" class="techo-list__body-table-button-view" @click="handleView(scope.$index,scope.row)">查看</el-button>
                    </template>
                </el-table-column> -->
            </el-table>
        </div>
        <div style="float:right;">
            <el-pagination ref="pager" @size-change="handlePagerChange" @current-change="handlePagerChange" layout="total, prev, pager, next, jumper"
                :total="recordCount">
            </el-pagination>
        </div>
    </el-dialog>
</template>

<script>
    import BScroll from 'better-scroll'

    export default {
        props: {
            // 页面标题
            title: {
                type: String,
                default: "网格长列表"
            },
            // 列表表头
            header: {
                type: Object
            },
        },
        data() {
            return {
                BS: null,
                // 组件可见性
                visible: false,
                // 列表记录总数
                recordCount: 0,
                data: []
            }
        },
        methods: {
            handleOpen(){
                this.scrollInit();
            },
            // 滚动条初始化
            scrollInit() {
                this.BS = new BScroll(this.$el.querySelector(".techo-list__body-table"), {
                    mouseWheel: true
                    // 如果你愿意可以打开显示滚动条
                    // scrollbar: {
                    //   fade: true,
                    //   interactive: false
                    // }
                })
            },
            // 滚动条销毁
            scrollDestroy() {
                try {
                    this.BS.destroy()
                } catch (e) {
                    delete this.BS
                    this.BS = null
                }
            },
            handlePagerChange() {
                let page = this.$refs['pager'].internalCurrentPage ? this.$refs['pager'].internalCurrentPage : 1;
                this.$emit('onPageChange', page)
            }
        },
        beforeDestroy() {
            this.scrollDestroy()
        }
    }
</script>

