                let that = this;
                service.getPeopleCountByCode({
                    code: this.currentRegion.code,
                    successFunc: (data) => {
                        that.countData["户籍人口"] = data.peopleCount;
                        that.countData["流动人口"] = data.flowCount;
                        that.countData["特殊人群"] = data.specialCount;
                        that.countData["重点青少年"] = data.youngCount;
                        that.countData["留守人员"] = data.rearCount;
                        that.countData["境外人员"] = data.overseasCount;
                    }
                })
            },
