                    code: this.currentRegion.code,
                    successFunc: (data) => {
                        that.countData["出租房"] = data.rentalHouseCount;
                        that.countData["企业"] = data.companyCount;
                        that.countData["场所"] = data.socialCount;
                        that.countData["学校"] = data.schoolCount;
                    }
                })
            },
