            handleQueryShiStatic() {

            },
            // 获取【物】统计信息
            handleQueryWuStatic() {

            },
            // 获取【情】统计信息
            handleQueryQingStatic() {

            },
            // 获取统计列表信息
            handleViewList(item, page) {
                let that = this;
                page = page ? page : 1;


                this.$store.commit('techo/map/activedRightSet', 'mapData')
                this.$store.commit('techo/map/themeClassSet', 'theme_data')
                // 完善所有人地事物情数据
                switch (item) {
                    case "户籍人口":
                    case "流动人口":
                    case "留守人员":
                    case "境外人员":
                    case "出租房":
                    case "企业":
                    case "学校":
                    case "场所":
                    case "重点青少年":
                    case "特殊人群":
                        this.$store.commit('techo/map/queryDataTypeSet', item)
                        break;
                }
            },
            // 分页查询
            handlePageChange(item, page) {
                this.handleViewList(item, page)
            }
        },
        watch: {
            currentRegion(nValue, oValue) {
                this.handleQueryRenStatis();
                this.handleQueryDiStatic();
                this.handleQueryShiStatic();
                this.handleQueryWuStatic();
                this.handleQueryQingStatic();
            }
        }
    }
</script>

