<template>
    <div class="map-theme-data-wrapper">
        <div class="map-theme-data-header">
            <img src="">
            <span>资源专题</span>
            <span class="map-theme-data-header-tool">
                <img class="map-theme-data-header-tool-img" alt="关闭" @click="handleClose" :src="imgDelete">
            </span>
        </div>
        <div class="map-theme-data-type">
            <div @click="type='ICON'" :class="type=='ICON'?'map-theme-data-type-item-active':''" class="map-theme-data-type-item">
                <img :src="type=='ICON'?imgIconClick:imgIcon" />
                <span>图标</span>
            </div>
            <div @click="type='HEXAGON'" :class="type=='HEXAGON'?'map-theme-data-type-item-active':''" class="map-theme-data-type-item">
                <img :src="type=='HEXAGON'?imgHexClick:imgHex" />
                <span>蜂窝</span>
            </div>
            <div @click="type='HEAT'" :class="type=='HEAT'?'map-theme-data-type-item-active':''" class="map-theme-data-type-item">
                <img :src="type=='HEAT'?imgHeatClick:imgHeat" />
                <span>热力</span>
            </div>
        </div>
        <div class="map-theme-data-content">
            <el-tree ref="tree" :render-content="renderTreeContent" :data="themeData" node-key="code" class="map-theme-data-tree"
                show-checkbox default-expand-all check-on-click-node @check-change="handleCheckChange"></el-tree>
        </div>
    </div>
</template>

<script>
    import {
        mapState,
        mapActions,
        mapGetters
    } from 'vuex'
    import BScroll from 'better-scroll'
    import service from '../../theme_service'

    export default {
        props: {
            mapApi: {
                type: Object
            }
        },
        data() {
            return {
                BS: null,
                imgDelete: require("./images/cont_eject_delete.png"),
                type: 'ICON',
                imgIcon: require("./images/图标默认.png"),
                imgIconClick: require("./images/图标点击.png"),
                imgHex: require("./images/蜂窝默认.png"),
                imgHexClick: require("./images/蜂窝点击.png"),
                imgHeat: require("./images/热力默认.png"),
                imgHeatClick: require("./images/热力点击.png"),
            }
        },
        computed: {
            ...mapState('techo/menu', [
                'aside'
            ]),
            // 当前的区域信息
            ...mapGetters('techo/map', ['currentRegion']),
            ...mapState('techo/map', [
                'activedRightSet', 'themeData', 'activedRight', 'themeType', 'themeClass'
            ])
        },
        mounted() {
            // 初始化专题图层数据
            if (this.themeData.length == 0) {
                this.initThemeData()
            }

            this.scrollInit();
        },
        methods: {
            ...mapActions('techo/map', [
                'initThemeData'
            ]),
            handleCheckNode(key) {
                this.$refs["tree"].setCheckedKeys(key, true);
            },
            // TODO 后期优化某些类型图层可支持的专题数据
            // 专题图层选则改变
            handleCheckChange(data, checked, indeterminate) {
                let checkNodes = this.$refs["tree"].getCheckedNodes();

                if (data.children == undefined) {
                    // 蜂窝图和热力图只能同时显示一个图层
                    if ((this.themeType == "HEXAGON" || this.themeType == "HEAT") && checkNodes.length > 1) {
                        const otherCode = _.find(checkNodes, item => item.code != data.code).code
                        this.$refs["tree"].setChecked(otherCode, false)
                    }

                    if (checked) {
                        this.$store.commit('techo/map/addActivedLayer', data);
                        this.$emit('onLayerAdd', data)
                    } else {
                        this.$store.commit('techo/map/removeActivedLayer', data);
                        this.$emit('onLayerRemove', data)
                    }
                }
            },
            renderTreeContent(h, {
                node,
                data,
                store
            }) {
                if(node.level==1){
                    return (
                    <span class='el-tree-node__label'>
                        <span>{node.label}</span>
                    </span>);
                }else if(node.level==2) {
                    let img = require('./images/'+data.label+".png");
                    return (
                    <span class='el-tree-node__label'>
                        <span>{node.label}</span>
                        <span>
                        <img style="vertical-align:middle;margin-left:10px;" src={img} />
                        </span>
                    </span>);
                }
            },
            // 滚动条初始化
            scrollInit() {
                this.BS = new BScroll(this.$el.querySelector(".map-theme-data-content"), {
                    mouseWheel: true
                })
            },
            // 滚动条销毁
            scrollDestroy() {
                try {
                    this.BS.destroy()
                } catch (e) {
                    delete this.BS
                    this.BS = null
                }
            },
            // 关闭面板
            handleClose() {
                this.$store.commit('techo/map/activedRightSet', '');
            },
            // 初始化已选择专题图层
            initThemeLayer() {
                let that = this
                let checkNodes = this.$refs["tree"].getCheckedNodes();

                _.map(checkNodes, item => {
                    that.$emit('onLayerAdd', item)
                })
            },
            // 清空已加载专题图层
            clearThemeLayer() {
                let that = this
                let checkNodes = this.$refs["tree"].getCheckedNodes();
                if (this.themeType == "ICON") {
                    _.map(checkNodes, item => {
                        that.mapApi.markerApi.clearMarkers(item.code)
                    })
                } else if (this.themeType == "HEXAGON" || this.themeType == "HEAT") {
                    _.map(checkNodes, item => {
                        that.mapApi.map.removeLayer(item.code + "_" + this.themeType)
                        that.mapApi.map.removeSource(item.code + "_" + this.themeType)
                    })
                }
            }
        },
        beforeDestroy() {
            // 页面销毁时清除地图标记
            this.mapApi.markerApi.clearMarkers();
            this.scrollDestroy()
        },
        watch: {
            // 监听专题图层面板关闭，清空选中数据
            activedRight(nValue, oValue) {
                if (nValue == "") {
                    let checkNodes = this.$refs["tree"].getCheckedNodes();
                    _.map(checkNodes, node => {
                        this.$refs["tree"].setChecked(node.code, false)
                    })
                }
            },
            currentRegion(nValue, oValue) {

            },
            themeClass(nValue, oValue) {
                if (nValue != 'theme_resource') {
                    // 初始化面板时查询加载数据
                    this.clearThemeLayer()
                } else if (nValue == 'theme_resource') {
                    // 清除地图marker点
                    this.initThemeLayer()
                }
            },
            // 修改专题图层类型
            type(nValue, oValue) {
                this.$store.commit('techo/map/themeTypeSet', nValue)
            },
            // 专题图层类型改变后清空选择图层
            themeType(nValue, oValue) {
                let that = this
                if (oValue) {
                    let checkNodes = this.$refs["tree"].getCheckedNodes();
                    if (oValue == "ICON") {
                        _.map(checkNodes, item => {
                            mapApi.markerApi.clearMarkers(item.code)
                            that.$refs["tree"].setChecked(item.code, false)
                        })
                    } else if (oValue == "HEXAGON" || oValue == "HEAT") {
                        _.map(checkNodes, item => {
                            mapApi.map.removeLayer(item.code + "_" + oValue)
                            that.$refs["tree"].setChecked(item.code, false)
                        })
                    }
                }
            }
        }
    }
</script>

<style lang="scss">
    @import './styles/theme-data.scss';
</style>