<template>
    <div class="map-house-info-frame">
        <div class="map-house-info-frame-left">
            <div class="map-house-info-frame-left-title">
                <span @click="handleChangeAll" :class="[floorIndex==-1&unitIndex==-1?'map-house-info-all-active':'map-house-info-all']">全部</span>
            </div>
            <div class="map-house-info-frame-left-content">
                <ul>
                    <li @click="handleChangeFloor(index)" :class="[floorIndex==index?'map-house-info-frame-floor-active':'map-house-info-frame-floor']"
                        v-for="(item,index) in floorInfo">
                        {{item}}
                    </li>
                </ul>
            </div>
        </div>
        <div class="map-house-info-frame-right">
            <div class="map-house-info-frame-right-title">
                <span @click="handleChangeUnit(index)" :class="[unitIndex==index?'map-house-info-frame-unit-active':'map-house-info-frame-unit']"
                    v-for="(item,index) in unitInfo">
                    {{item}}
                </span>
            </div>
            <div class="map-house-info-frame-right-content">
                <div>
                    <el-row @click.native="handleHouseClick(item)" class="map-house-info-frame-right-item" v-for="(item,index) in houseInfo"
                        :key="index" type="flex" justify="space-around" :class="[activedHouseHolder==item?'map-house-info-frame-right-item-active':'']">
                        <el-col :span="6">
                            <div><img :src="imgHouse" alt=""></div>
                            <div style="letter-spacing:1px;font-weight:600;">{{item.houseNum}}</div>
                        </el-col>
                        <el-col :span="5">
                            <div>户主</div>
                            <div class="map-house-info-frame-color-green">{{item.householderName}}</div>
                        </el-col>
                        <el-col :span="4">
                            <div>人数</div>
                            <div class="map-house-info-frame-color-green">{{item.peopleCount}}</div>
                        </el-col>
                        <el-col :span="4">
                            <div>类型</div>
                            <div class="map-house-info-frame-color-green">自有</div>
                        </el-col>
                        <el-col :span="5">
                            <el-button size="mini" class="map-house-frame-button-view" @click="handleView(item)">查看</el-button>
                        </el-col>
                    </el-row>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import {
        mapState,
        mapGetters
    } from 'vuex'
    import BScroll from 'better-scroll'
    import service from "../../../map_service"

    export default {
        data() {
            return {
                imgHouse: require('../images/cont_icon_home_defaule.png'),
                imgHouseSelect: require('../images/cont_icon_home_select.png'),
                // 层数
                floorIndex: -1,
                // 单元数
                unitIndex: -1,
                // 房屋户信息
                houseAllInfo: [],
                houseInfo: [],
                // 层数列滚动轴
                BS1: null,
                BS2: null
            }
        },
        computed: {
            ...mapState('techo/map', ['activedNode', 'activedHouse', 'activedHouseHolder']),
            ...mapGetters('techo/map', ['currentRegion']),
            // 单元信息
            unitInfo() {
                return _.map(new Array(this.activedHouse.unitCount), (item, index) => {
                    return (index + 1) + '单元'
                })
            },
            // 楼层信息
            floorInfo() {
                return _.map(new Array(this.activedHouse.floorCount), (item, index) => {
                    return (index + 1) + '层'
                })
            }
        },
        mounted() {
            this.scrollInitOne()

            this.handleQuery();
        },
        methods: {
            // 查询房屋户信息
            handleQuery() {
                let that = this
                service.getHouseHolderInfo({
                    id: this.activedHouse.id,
                    unit: this.unitIndex + 1,
                    floor: this.floorIndex + 1,
                    successFunc: data => {
                        // that.houseAllInfo = _.cloneDeep(data)
                        that.houseInfo = data
                    }
                })
            },
            // 滚动条初始化
            scrollInitOne() {
                this.BS1 = new BScroll(this.$el.querySelector(".map-house-info-frame-left-content"), {
                    mouseWheel: true
                })
            },
            scrollInitTwo() {
                this.BS2 = new BScroll(this.$el.querySelector(".map-house-info-frame-right-content"), {
                    mouseWheel: true
                })
            },
            // 滚动条销毁
            scrollDestroy() {
                try {
                    this.BS1 && this.BS1.destroy()
                    this.BS2 && this.BS2.destroy()
                } catch (e) {
                    delete this.BS1
                    delete this.BS2
                    this.BS1 = null
                    this.BS2 = null
                }
            },
            // 选择全部房屋户
            handleChangeAll() {
                this.floorIndex = -1;
                this.unitIndex = -1;
                this.handleQuery()
            },
            // 选择楼层
            handleChangeFloor(index) {
                this.floorIndex = index;
                this.handleQuery()
            },
            // 选择单元
            handleChangeUnit(index) {
                this.unitIndex = index;
                this.handleQuery()
            },
            // 选择指定房屋户
            handleHouseClick(item) {
                this.$store.commit('techo/map/activedHouseHolderSet', item)
            }
        },
        beforeDestroy() {
            this.scrollDestroy()
        },
        watch: {
            houseInfo(nValue, oValue) {
                if (oValue.length == 0) {
                    this.$nextTick(function () {
                        this.scrollInitTwo()
                    })
                }
            },
            activedHouse(nValue,oValue){
                if(nValue){
                    this.handleQuery()
                }
            }
        }
    }
</script>

